@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.v-vlite {
  --vlite-colorPrimary: #39B8FF;
}

.v-vlite.v-video {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
}

.v-vlite.v-video.v-paused {
  background-color: #fff;
}

.v-bigPlay {
  display: none;
}
