@import '@styles/vars.scss';

.teacher-block {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    color: $black;
    width: 250px;

    @media only screen and (max-width: $bp-xl) {
        width: 220px;
    }

    @media only screen and (max-width: $bp-lg) {
        width: 200px;
    }

    &:hover &__img-wrapper::after{
        opacity: 0.5;
    }

    &:hover &__icon-wrapper{
        opacity: 1;
    }
    
    &:hover &__title{
        color: $black;
    }

    &__img-wrapper {
        display: block;
        position: relative;
        width: 228px;
        height: 228px;
        flex-shrink: 0;
        border-radius: 50%;

        @media only screen and (max-width: $bp-xl) {
            width: 190px;
            height: 190px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 155px;
            height: 155px;
        }

        &::after{
            content: "";
            display: block;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 1;
            background-color: #004E96;
            opacity: 0;
            transition: opacity 0.35s ease;
        }
    }

    &__img {
        position: relative;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        z-index: 1;
    }

    &__icon-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        right: 10px;
        bottom: 0;
        width: 58px;
        height: 58px;
        border-radius: 50%;
        background-color: $blue;
        fill: #fff;
        z-index: 2;
        opacity: 0;
        transition: opacity 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            width: 42px;
            height: 42px;
        }
    }

    &__icon {
        width: 32px;
        height: 32px;

        @media only screen and (max-width: $bp-lg) {
            width: 24px;
            height: 24px;
        }
    }

    &__subtitle {
        font-weight: 600;
        line-height: 150%;
        color: $black;
    }

    &__img-wrapper + &__subtitle{
        margin-top: 25px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 20px;
        }
    }

    &__img-wrapper + &__title{
        margin-top: 48px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 20px;
        }
    }

    &__title {
        color: $blue;
        margin-bottom: 10px;
        transition: color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 14px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 16px;
        }
    }

    &__description {
        color: #606466;
        line-height: 150%;
    }
}
