@import '@styles/vars.scss';

.about-courses {
    padding-top: 142px;
    
    @media only screen and (max-width: $bp-xl) {
        padding-top: 120px;
    }
    
    @media only screen and (max-width: $bp-lg) {
        padding-top: 104px;
    }
    

    &__inner {
    }

    &__title {
        margin-bottom: 50px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 40px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 30px;
        }
    }

    &__list {

        .list__item{
            margin-bottom: 2px;
        }
    }

    &__item {
        display: flex;
        flex-direction: column;
        position: relative;
        padding-left: 106px;
        margin-bottom: 64px;

        &:last-of-type{
            margin-bottom: 0;
        }

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 42px;
            padding-left: 80px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding-left: 0;
            margin-bottom: 32px;
        }
    }

    &__item-header {
        display: flex;
        margin-bottom: 22px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 18px;
        }
    }

    &__item-icon {
        object-fit: contain;
        width: 84px;
        height: 84px;
        position: absolute;
        left: 0;
        top: 0;

        @media only screen and (max-width: $bp-xl) {
            width: 64px;
            height: 64px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 54px;
            height: 54px;
            position: static;
            margin-right: 15px;
        }
    }

    &__item-title {
        line-height: 180%;

        @media only screen and (max-width: $bp-md) {
            font-size: 16px;
        }
    }

    &__item-body {
        position: relative;
        transition: max-height 0.5s ease;

        @media only screen and (max-width: $bp-lg) {
            max-height: 144px;
            overflow: hidden;
        }

        &.is-active{
            max-width: none;
        }
    }

    &__item-row{
        display: flex;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
        }
    }

    &__item-col {
        flex: 0 1 calc(50% - 32px);
        max-width: 432px;
        margin-right: 64px;

        @media only screen and (max-width: $bp-xl) {
            flex-basis: calc(50% - 15px);
            margin-right: 30px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            max-width: none;
            margin-right: 0;
            margin-bottom: 15px;

            &:last-of-type{
                margin-bottom: 0;
            }
        }

        &:last-of-type{
            margin-right: 0;
            margin-bottom: 0;
        }
    }

    &__item-text {
        line-height: 150%;

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
    }

    &__subtitle {
        margin-bottom: 22px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 18px;
            margin-bottom: 10px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 16px;
        }
    }

    &__item-more{
        display: none;
        cursor: pointer;
        position: absolute;
        z-index: 2;
        bottom: 0;
        right: 0;
        width: 60px;
        height: 30px;
        background:radial-gradient(70% 70% at 66.67% 96.67%, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%) ;

        @media only screen and (max-width: $bp-lg) {
            display: flex;
            justify-content: flex-end;
            align-items: flex-end;
        }

        .icon{
            width: 29px;
            height: 15px;
        }
    }
}
