@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.section-news {

    &__inner{
        display: flex;
        flex-direction: column;
    }

    &__title {
        margin-bottom: 64px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 48px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 32px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 24px;
        }
    }

    &__button-more {
        margin: 48px auto 0;

        @media only screen and (max-width: $bp-xl) {
            margin-top: 40px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-top: 32px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-top: 24px;
        }
    }

    &__list{
        display: flex;
        flex-wrap: wrap;
        margin: 0 -10px -70px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: -45px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin: 0 -8px -32px;
        }

        @media only screen and (max-width: $bp-md) {
            margin: 0 0 -24px;
        }

        &>*{
            width: calc(25% - 20px);
            margin: 0 10px 70px;
    
            @media only screen and (max-width: $bp-xl) {
                margin-bottom: 45px;
                width: calc(33.33% - 20px);
            }
    
            @media only screen and (max-width: $bp-lg) {
                margin: 0 8px 32px;
                width: calc(50% - 16px);
            }
    
            @media only screen and (max-width: $bp-md) {
                margin: 0 0 24px;
                width: 100%;

                &:nth-child(1),
                &:nth-child(2) {
                    order: -1;
                }
            }
        }
    }

    .card-article-new{
        &:first-of-type{
            @media only screen and (min-width: $bp-md + 1) {
                width: 100%;
                color: #fff;

                &::after{
                    content: "";
                    display: block;
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    z-index: 1;
                    background: linear-gradient(360deg, rgba(21, 127, 193, 0.7) 8.02%, rgba(58, 179, 254, 0) 65.78%)
                }

                .card-article-new{

                    &__img-wrapper{
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        height: 100%;
                    }
        
                    &__content{
                        padding: 15px 24px;
                        flex-direction: row;
                        align-items: center;
                        margin-top: auto;
                        flex-grow: 0;
                    }
            
                    &__title{
                        font-size: 20px;
                        margin-right: 15px;
                        max-width: 50%;
                        margin-bottom: 0;
                    }
            
                    &__info{
                        justify-content: flex-end;
                        margin-left: auto;
                    }
                    
                    &__name{
                        text-align: right;
            
                        span{
                            color: #fff;
                            opacity: 0.5;
                        }
                        
                        .link{
                            color: #fff;
                        }
                    }
                }
            }
            
            @media only screen and (min-width: $bp-lg + 1) {
                width: calc(66.66% - 20px);
            }
            
            @media only screen and (min-width: $bp-xl + 1) {
                width: calc(50% - 20px);
            }
        }

    }

    .card-socials{
        @media only screen and (max-width: $bp-md) {
            order: -1;
        }
    }
}
