@import '@styles/vars.scss';
.tooltip{
    display: inline-flex;
    align-items: center;
    position: relative;
    margin-left: 8px;
    fill: #B8B8B8;
    color: #b8b8b8;
    transition: color 0.35s ease, fill 0.35s ease;

    &__icon{
        cursor: pointer;
        width: 20px;
        height: 20px;
        flex-shrink: 0;
    }

    &:hover &__content{
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }
    
    &__content {
        position: absolute;
        bottom: 50%;
        left: 100%;
        padding: 10px 16px;
        visibility: hidden;
        opacity: 0;
        pointer-events: none;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0px 0px 44px #E1F4FF;
        width: 250px;
        font-size: 14px;
        color: #000;
        line-height: 120%;
        z-index: 1;
        transition: opacity 0.4s ease, visibility 0.4s ease;
    
        @media only screen and (max-width: $bp-lg) {
            right: 100%;
            left: auto;
        }
    }
}
