@import "@styles/vars.scss";

.section-tree{
    margin-bottom: 138px;

    @media only screen and (max-width: $bp-xl) {
        margin-bottom: 120px;
    }
    
    @media only screen and (max-width: $bp-lg) {
        margin-bottom: 134px;
    }
    
    @media only screen and (max-width: $bp-md) {
        margin-bottom: 76px;
    }

    &__inner{
        position: relative;
        height: 1268px;

        @media only screen and (max-width: $bp-lg) {
            height: 1545px;
            height: 1576px;
        }

        @media only screen and (max-width: $bp-md) {
            height: 1216px;
            height: 1208px;
        }
    }

    &__main{
        position: absolute;
        max-width: 425px;
        color: #000;
        top: 658px;
        left: 0;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__title{
        font-weight: 700;
        font-size: 30px;
        line-height: 130%;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 22px;
            margin-bottom: 10px;
        }
    }

    &__text{
        font-size: 16px;
        line-height: 150%;
        color: #797D80;

        @media only screen and (max-width: $bp-xl) {
            font-size: 12px;
        }
    }

    &__button{
        margin-top: 32px;
    }

    &__bg{
        pointer-events: none;
        position: absolute;
        width: auto;
        height: 100%;
        top: 0;
        left: 50%;
        transform: translateX(-50%);

        @media only screen and (max-width: $bp-lg) {
            left: auto;
            right: -422px;
            transform: none;
        }

        @media only screen and (max-width: $bp-md) {
            right: -276px;
        }

        @media only screen and (max-width: $bp-sm) {
            right: -386px;
        }

        img{
            height: 100%;
        }
    }
}