@import '@styles/vars.scss';
@import '@styles-mixins/media.scss';
@import '@styles-mixins/hoverEffects.scss';

.section-reviews {
    &__outer-indent { 
        margin-bottom: 100px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 120px;
        }
    }

    &__header {
        display: flex;
        margin-bottom: 56px;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 48px;
        }

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            margin-bottom: 40px;
        }
    }

    &__title{
        margin-right: 32px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 24px;
            margin-right: 0;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 20px;
        }
    }

    &__info {
        display: flex;
        align-items: center;
    }

    &__info-icon {
        width: 34px; 
        height: 34px;
        flex-shrink: 0;
        margin-right: 16px;
        fill: #D6D6D6;
    }

    &__info-text {
        font-weight: 600;
        font-size: 14px;
        line-height: 120%;
        color: $gray-dark;
        max-width: 224px;

        @media only screen and (max-width: $bp-lg) {
            max-width: 176px;
        }

        @media only screen and (max-width: $bp-md) {
            max-width: 400px; 
        }
    }

    &__slider{
        .slider{
            
            &__navigation{
                @media only screen and (max-width: $bp-lg) {
                    display: none;
                }
            }

            &__button{
                margin-right: 16px;

                &:last-of-type{
                    margin-right: 0;
                }
            }

            &__pagination{
                display: none;

                @media only screen and (max-width: $bp-lg) {
                    display: flex;
                }
            }
        }
    }
}