@import '@styles/vars.scss';

.card-course {
    box-sizing: border-box;
    position: relative;
    display: flex;
    flex-direction: column;
    text-decoration: none;
    background-color: #fff;
    color: $black;
    border-radius: 10px;
    width: 100%;
    min-height: 220px;
    padding: 32px 42px 24px;
    overflow: hidden;
    transition: background-color 0.5s ease;
    
    @media only screen and (max-width: $bp-lg) {
        padding: 26px 32px;
    }

    @media only screen and (max-width: $bp-md) {
        min-height: 424px;
        padding: 22px;
    }

    &:hover{
        background-color: $blue;
    }

    &:hover &__title{
        color: #fff;
    }

    &:hover &__level{
        color: #fff;
    }

    &:hover &__label{
        background-color: #fff;
        color: $blue;
    }

    &:hover &__time{
        fill: #fff;
        color: #fff;
    }

    &:hover &__tooltip{
        fill: #fff;
        color: #fff;
    }

    &__inner {
        position: relative;
        z-index: 2;
        flex-grow: 1;
        display: flex;
        flex-direction: column;

    }

    &__header {
        margin-bottom: 30px;
    }

    &__title-wrapper {
        display: inline-flex;
        align-items: center;
        margin-bottom: 5px;
    }

    &__title {
        color: $black;
        transition: color 0.35s ease;
    }

    &__tooltip {
    }

    &__level {
        color: #A4A4A4;
        transition: color 0.5s ease;
    }

    &__body {
        margin-top: auto;
        display: flex;
        align-items: center;
    }

    &__labels {
        display: flex;
        align-items: center;
    }

    &__label {
        display: inline-flex;
        align-items: center;
        justify-content: center;
        height: 32px;
        padding: 0 18px;
        border-radius: 20px;
        background-color: $blue;
        font-weight: 700;
        color: #fff;
        margin-right: 10px;
        transition: background-color 0.35s ease, color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            margin-right: 8px;
        }

        &:last-of-type{
            margin-right: 16px;
        }

        &--online{
            background-color: #00EBBA;
        }
    }

    &__time {
        display: inline-flex;
        align-items: center;
        font-family: 'Inter';
        fill: $blue;
        color: $blue;
        transition: color 0.35s ease, fill 0.35s ease;

        .icon{
            width: 24px;
            height: 24px;
            margin-right: 4px;
        }
    }

    &__img {
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        max-width: 282px;
        width: auto;

        @media only screen and (max-width: $bp-md) {
            height: 284px;
            width: 100%;
            max-width: none;
            bottom: 0;
            top: auto;
        }

        &::before{
            content: none;
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(0deg, #FFFFFF 25%, rgba(255, 255, 255, 0) 60%);
            z-index: 1;
            transition: opacity 0.35s ease;

            @media only screen and (max-width: $bp-md) {
                content: "";
            }
        }

        &::after{
            content: none;
            position: absolute;
            top: 0;
            right: 0;
            width: 100%;
            height: 100%;
            background: linear-gradient(0deg, $blue 25%, rgba(255, 255, 255, 0) 60%);
            z-index: 1;
            opacity: 0;
            transition: opacity 0.35s ease;

            @media only screen and (max-width: $bp-md) {
                content: "";
            }
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: cover;

            @media only screen and (max-width: $bp-md) {
                object-position: top left;
                object-fit: contain;
            }
        }
    }

    &:hover &__img{
        &::before{
            opacity: 0;
        }
        &::after{
            opacity: 1;
        }
    }
}
