@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.badges {
    display: inline-flex;
    align-items: center;
    padding: 4px 3px;
    border-radius: 50px;
    background-color: #fff;
    overflow: auto hidden;
    width: auto;
    max-width: 100%;

    &__item {
        margin-right: 8px;

        &:last-of-type{
            margin-right: 0;
        }
    }
}
.badge {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 42px;
    padding: 0 24px;
    border-radius: 40px;
    text-decoration: none;
    color: rgba(0, 77, 150, 0.3);
    font-weight: 700;
    font-size: 14px;
    line-height: 120%;
    letter-spacing: 0.1px;
    background-color: transparent;
    border: 2px solid transparent;
    transition: color 0.25s ease 0.1s, fill 0.25s ease 0.1s, background-color 0.35s ease, border-color 0.35s ease;

    &__icon.icon{
        width: 24px;
        height: 24px;
    }

    &__text + &__icon{
        margin-left: 8px;
    }

    &__icon + &__text{
        margin-left: 8px;
    }

    &:hover {
        color: $blue;
        border-color: $blue;
    }
    &.is-active {
        pointer-events: none;
        background-color: $blue;
        color: #fff;
    }

}