@import '@styles/vars.scss';
@import '@styles-mixins/media.scss';

.modal {
  font-family: inherit;

  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
    background: rgba(0,0,0,0.6);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__container {
    display: flex;
    flex-direction: column;
    background-color: #fff;
    padding: 24px 16px 32px;
    width: 100%;
    height: 100%;
    max-width: 640px;
    max-height: 100vh;
    overflow-y: auto;
    box-sizing: border-box;

    @include media(from_sm) {
      padding: 24px 32px;
      height: auto;
      max-height: unset;
    }

    @include media(from_l) {
      padding: 32px 32px 40px;
    }
  }

  &__container_type_video {
    padding-top: 0;
    padding-left: 0;
    padding-right: 0;
    width: 100%;
    max-width: unset;

    @include media(from_sm) {
      width: auto;
    }
  }
  
  &__header {
    position: relative;
    z-index: 20;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;

    @include media(from_sm) {
      margin-bottom: 24px;
    }

    @include media(from_m) {
      margin-bottom: 16px;
    }

    @include media(from_l) {
      margin-bottom: 24px;
    }
  }

  &__title {
    margin-top: 0;
    margin-bottom: 0;
    margin-right: 48px;
    font-weight: 600;
    font-size: 16px;
    line-height: 150%;
    color: #000;
    box-sizing: border-box;

    @include media(from_sm) {
      font-size: 18px;
      margin-right: 56px;
    }

    @include media(from_m) {
      margin-right: 48px;
    }

    @include media(from_l) {
      margin-right: 56px;
    }
  }

  &__close {
    margin-left: auto;

    width: 32px;
    height: 32px;
  
    background-size: 30px auto;
  
    @include media(from_sm) {
      width: 48px;
      height: 48px;
      background-size: 46px auto;
    }
  
    @include media(from_m) {
      width: 32px;
      height: 32px;
    
      background-size: 30px auto;
    }
  
    @include media(from_l) {
      width: 48px;
      height: 48px;

      background-size: 46px auto;
    }
  }

  &__content {
    margin-bottom: 24px;
    line-height: 150%;
    color: #000;

    @include media(from_sm) {
      margin-bottom: 32px;
    }

    @include media(from_m) {
      margin-bottom: 24px;
    }

    @include media(from_l) {
      margin-bottom: 40px;
    }
  }

  &__content_type_video {
    position: relative;
    width: 100%;
    height: 100%;

    @include media(from_sm) {
      width: 640px;
      height: 360px;
    }

    @include media(from_l) {
      width: 854px;
      height: 480px;
    }
  }

  &__footer {
    margin-top: auto;
  }
  
  &__button {
    margin: 0 auto;
  }
}

@keyframes mmfadeIn {
    from { opacity: 0; }
      to { opacity: 1; }
}

@keyframes mmfadeOut {
    from { opacity: 1; }
      to { opacity: 0; }
}

@keyframes mmslideIn {
  from { transform: translateY(15%); }
    to { transform: translateY(0); }
}

@keyframes mmslideOut {
    from { transform: translateY(0); }
    to { transform: translateY(-10%); }
}

.micromodal-slide {
  display: none;
}

.micromodal-slide.is-open {
  display: block;
}

.micromodal-slide[aria-hidden="false"] .modal__overlay {
  animation: mmfadeIn .3s cubic-bezier(0.0, 0.0, 0.2, 1);
}

.micromodal-slide[aria-hidden="false"] .modal__container {
  animation: mmslideIn .3s cubic-bezier(0, 0, .2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal__overlay {
  animation: mmfadeOut .3s cubic-bezier(0.0, 0.0, 0.2, 1);
}

.micromodal-slide[aria-hidden="true"] .modal__container {
  animation: mmslideOut .3s cubic-bezier(0, 0, .2, 1);
}

.micromodal-slide .modal__container,
.micromodal-slide .modal__overlay {
  will-change: transform;
}


.modal {
  &__preloader {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
  }

  &__preloader-box {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  
  &__preloader-ellipse {
    position: absolute;
    top: 33px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #39B8FF;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
  }
  
  &__preloader-ellipse:nth-child(1) {
    left: 8px;
    animation: lds-ellipsis1 0.6s infinite;
  }
  
  &__preloader-ellipse:nth-child(2) {
    left: 8px;
    animation: lds-ellipsis2 0.6s infinite;
  }
  
  &__preloader-ellipse:nth-child(3) {
    left: 32px;
    animation: lds-ellipsis2 0.6s infinite;
  }
  
  &__preloader-ellipse:nth-child(4) {
    left: 56px;
    animation: lds-ellipsis3 0.6s infinite;
  }

  & .video__pic-wrapper {
    background-color: #fff;
  }

  & .video__pic-wrapper::after {
    content: none;
  }
}


@keyframes lds-ellipsis1 {
  0% {
    transform: scale(0);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes lds-ellipsis3 {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(0);
  }
}

@keyframes lds-ellipsis2 {
  0% {
    transform: translate(0, 0);
  }
  100% {
    transform: translate(24px, 0);
  }
}
