@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.section-purpose{
    margin-bottom: 318px;
    
    @media only screen and (max-width: $bp-xl) {
        margin-bottom: 235px;
    }

    @media only screen and (max-width: $bp-lg) {
        margin-bottom: 212px;
    }

    @media only screen and (max-width: $bp-md) {
        margin-bottom: 14px;
    }
    
    &__inner{
        margin: 0 auto;
        width: 100%;
        max-width: 795px;

        @media only screen and (max-width: $bp-xl) {
            max-width: 605px;
        }

        @media only screen and (max-width: $bp-lg) {
            max-width: 514px;
        }
    }

    &__title{
        padding: 0;
        margin: 0;
        text-transform: uppercase;
        text-align: center;
        font-weight: 700;
        font-size: 16px;
        line-height: 150%;
        letter-spacing: 5px;
        color: #C1C8CC;
        margin-bottom: 25px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 30px;
        }
    }

    &__text{
        text-align: center;
        letter-spacing: 0.15px;
        color: #000;
    }
}