@import '@styles/vars.scss';

.section-catalog {
    padding: 50px 0 130px;

    @media only screen and (max-width: $bp-lg) {
        padding: 35px 0 100px;
    }

    &__container{
        padding: 0 40px;
    }

    &__inner {
        display: flex;
        flex-wrap: wrap;
    }

    &__title {
        width: 100%;
        margin-bottom: 38px;

        @media only screen and (max-width: $bp-lg) {
            max-width: calc(50% - 10px);
            margin-right: 20px;
        }
    }

    &__filter {
        width: 332px;
        margin-right: 90px;

        @media only screen and (max-width: $bp-xl) {
            width: 315px;
            margin-right: 40px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            margin-right: 0;
            max-width: calc(50% - 10px);
        }
    }

    &__main {
        width: 100%;
        margin-top: -46px;
        max-width: calc(100% - 422px);
        margin-left: auto;

        @media only screen and (max-width: $bp-xl) {
            max-width: calc(100% - 355px);
        }

        @media only screen and (max-width: $bp-lg) {
            max-width: none;
            margin-top: 0;
        }
    }

    &__list {
    }

    &__group {
        margin-bottom: 60px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 70px;
        }
        
        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__category-title {
        margin-bottom: 32px;
        text-align: right;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 25px;
        }
    }

    &__card {
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 25px;
        }

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__pagination {
    }
}