@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

@mixin defaultHover() {
  transition: 0.2s ease-in-out;

  &:active {
    opacity: 0.64;
  }

  @include media(from_l) {
    &:hover {
      opacity: 0.64;
      cursor: pointer;
    }
  }
}

@mixin fillHover($fill, $textColor) {
  transition: 0.2s ease-in-out;

  &:active {
    background-color: $fill;
    color: $textColor;
  }

  @include media(from_l) {
    &:hover {
      background-color: $fill;
      color: $textColor;
      cursor: pointer;
    }
  }
}
