@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.input{
    background: none;
    border: none;
    outline: none;
    display: flex;
    width: 100%;
    color: $blue;
    padding: 0 8px 8px;
    font-weight: 400;
    font-family: "Inter";
    font-size: 14px;
    background-color: transparent;
    border-bottom: 3px solid $blue;

    &::placeholder{
        color: inherit;
        opacity: 0.5;
    }

    &--white{
        color: #fff;
        border-color: #fff;
    }
}