@import "@styles/vars.scss";

.sitemap {
  display: flex;
  flex-direction: column;

  &__link {
    display: flex;
    align-items: center;
    height: 32px;
    padding: 8px 8px 8px 0;

    text-decoration: none;
    color: $blue;
    font-weight: 600;

    transition: 0.2s ease-in-out;

    &:hover {
      padding: 8px;
      opacity: 0.64;
    }
  }
}
