@import '@styles/vars.scss';

.section-teachers{
    position: relative;
    padding-top: 120px;

    @media only screen and (max-width: $bp-lg) {
        padding-top: 105px;
    }

    &__title{
        font-size: 130px;
        line-height: 120%;
        letter-spacing: 0.22px;
        color: $blue;
        display: inline-flex;
        flex-direction: column;
        font-weight: 800;
        width: 250px;

        span{
            font-weight: 700;
            font-size: 65px;
            line-height: 100%;
            margin-top: -25px;
        }

        @media only screen and (max-width: $bp-xl) {
            font-size: 110px;

            span{
                font-size: 58px;
            }
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 95px;
            margin-bottom: 40px;
            flex-direction: row;
            align-items: center;

            span{
                margin-left: 15px;
                margin-top: 0;
                font-size: 50px;
            }
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 85px;

            span{
                font-size: 40px;
            }
        }
    }

    &__content{
        margin: 0 auto;
    }

    &__inner{
        position: relative;
        width: 100%;
        max-width: 930px;
        margin: 0 auto;

        @media only screen and (max-width: $bp-xl) {
            max-width: none;
        }
    }

    &__slider{
        width: 100%;
        margin-top: -154px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 0;
        }

        @media only screen and (min-width: $bp-lg + 1) {
            .slider{
                &__wrapper{
                    display: block;
                    width: 100%;
                    box-sizing: border-box;
                    display: flex;
                    justify-content: center;
                    flex-wrap: wrap;
    
                    &::after{
                        content: "";
                        display: flex;
                        margin: 80px 30px 0;
                        width: 250px;
                        order: 1;
    
                        @media only screen and (max-width: $bp-xl) {
                            margin: 0 20px;
                            display: none;
                        }
                    }
                }
    
                &__slide{
                    box-sizing: border-box;
                    height: auto;
                    margin-right: 30px;
                    margin-left: 30px;
                    width: 250px;
                    margin-top: 80px;
    
                    @media only screen and (max-width: $bp-xl) {
                        margin-right: 20px;
                        margin-left: 20px;

                        @media only screen and (max-width: 1050px) {
                            width: 220px;
                        }
                    }
    
                    @media only screen and (max-width: $bp-lg) {
                        width: 200px;
                    }
                    
                    &:nth-of-type(1),
                    &:nth-of-type(3){
                        margin-top: 225px;
                    }
           
    
                    &:nth-of-type(3n + 2):not(:last-of-type){
                        margin-top: -145px;
                    }
    
                    &:nth-of-type(2){
                        margin-top: 0 !important;
                    }
    
                    &:last-of-type{
                        order: 2;
                        margin-top: -145px;
    
                        @media only screen and (max-width: $bp-xl) {
                            margin-top: 80px;
                        }
                    }
                }
            }
        }

        .slider{
            &__pagination{
                margin-top: 20px;
            }
        }
    }

    .teacher-block{
        width: 100%;
    }

    .teachers-menu{
        position: relative;
        z-index: 2;
        // margin-right: -95px;
        // margin-left: auto;
        // transform: translateY(calc(-100% - 65px));
        position: absolute;
        bottom: 65px;
        right: -95px;

        @media only screen and (max-width: $bp-xl) {
            display: none;
        }
    }
}