@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.section-offers {
    margin-bottom: 158px;

    @media only screen and (max-width: $bp-xl){
        margin-bottom: 124px;
    }

    @media only screen and (max-width: $bp-lg){
        margin-bottom: 210px;
    }

    @media only screen and (max-width: $bp-md){
        margin-bottom: 120px;
    }

    &__header {
        margin-bottom: 45px;

        @media only screen and (max-width: $bp-xl){
            margin-bottom: 24px;
        }
        
        @media only screen and (max-width: $bp-lg){
            margin-bottom: 32px;
        }
        
        @media only screen and (max-width: $bp-md){
            margin-bottom: 20px;
        }
    }

    &__inner {
    }

    &__slider {
        .slider {

            &__inner {
                overflow: visible;
            }
    
            &__slide {
                opacity: 0;
                transition: opacity 0.5s ease;
    
                &.swiper-slide-active{
                    opacity: 1;
                }
            }

            &__navigation{
                width: auto;
            }

            &__pagination{
                margin: 0 32px;
                
                @media only screen and (max-width: $bp-lg){
                    width: 100%;
                    margin: 0;
                }
            }

            &__button{
                @media only screen and (max-width: $bp-lg){
                    display: none;
                }
            }
        }
    }
}
