@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";
@import "@styles-mixins/hoverEffects.scss";

// до 413; после 338

.footer {
    padding: 30px 0 30px;
    padding-bottom: 30px;
    background-color: #fff;

    @media only screen and (max-width: $bp-lg) {
        padding: 35px 0 16px;
    }

    &__container {
        max-width: 1615px;
    }

    &__sitemap {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        padding: 30px 100px 50px;

        @media only screen and (max-width: $bp-xxl) {
            padding: 20px 0 30px;
        }

        @media only screen and (max-width: $bp-xl) {
            display: none;
        }
    }

    &__menu {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        padding: 0 20px;

        @media only screen and (max-width: $bp-xxl) {
            padding: 0 15px;
        }

        &:nth-of-type(1){
            align-items: center;
            width: 260px;
            flex-grow: 0;

            @media only screen and (max-width: $bp-xxl) {
                align-items: start;
                width: 220px;
                padding-left: 0;
            }
        }

        &:nth-of-type(2){
            width: calc((100% - 520px) / 2);

            @media only screen and (max-width: $bp-xxl) {
                width: calc((100% - 440px) / 2);
            }
        }

        &:nth-of-type(3){
            width: calc(100% - ((100% - 520px) / 2) - 260px);

            @media only screen and (max-width: $bp-xxl) {
                width: calc(100% - ((100% - 440px) / 2) - 220px);
            }
        }
    }

    &__menu--lg &__nav{
        display: block;
        column-count: 2;
        column-gap: 50px;
    }

    &__nav {
        padding: 0;
        margin: 0;
    }

    &__nav-item {
        list-style: none;
        padding: 0;
        margin: 0 0 10px;

        &:last-of-type{
            margin-bottom: 0;
        }
    }

    &__nav-link {
       
    }

    &__menu-title {
        margin-bottom: 12px;
    }

    &__main {
        padding: 30px 100px 0;
        display: flex;
        
        @media only screen and (max-width: $bp-xxl) {
            padding-right: 0px;
            padding-left: 0px;
        }

        @media only screen and (max-width: $bp-xl) {
            flex-wrap: wrap;
            padding-top: 0;
        }
    }

    &__info {
        margin-top: 25px;
        width: 240px;
        flex-shrink: 0;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        
        @media only screen and (max-width: $bp-xxl) {
            width: 200px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 50%;
            order: 2;
            margin-top: 0;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
        }
    }

    &__policy {
        margin-bottom: 30px;

        @media only screen and (max-width: $bp-lg) {
            max-width: 240px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 40px;
        }

        .link{
            display: inline;
        }
    }

    &__rights {
        display: flex;
        align-items: center;

        span{
            margin-right: 8px;

            &:first-of-type{
                flex-shrink: 0;
            }

            &:last-of-type{
                margin-right: 0; 
            }
        }
    }

    &__contacts {
        display: flex;
        flex-wrap: wrap;
        align-content: space-between;
        margin: 0 20px -30px;

        @media only screen and (max-width: $bp-xxl) {
            margin: 0 15px -20px;
        }

        @media only screen and (max-width: $bp-xl) {
            width: 100%;
            margin: 0 0 20px;
        }
        
        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 40px;
        }
    }

    &__contacts-item {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        margin: -10px 0 30px;
        padding: 0 20px;
        width: 50%;

        @media only screen and (max-width: $bp-xxl) {
            padding: 0 15px;
            margin-bottom: 20px;
        }

        @media only screen and (max-width: $bp-xl) {
            padding: 0 30px 0 0;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            flex-direction: column;
            flex-wrap: wrap;
            margin-bottom: 25px;
            padding: 0;

            &:last-of-type{
                margin-bottom: 0;
            }
        }

        &:nth-of-type(3){
            flex-direction: row;
            flex-wrap: wrap;

            @media only screen and (max-width: $bp-lg) {
                order: -1;
            }
        }

        &>*{
            margin-top: 10px;

            @media only screen and (max-width: $bp-lg) {
                margin-top: 6px;
            }
        }


    }

    &__contacts-item:nth-of-type(3) &__contacts-title{
        margin-right: 25px;
    }

    &__contacts-link {
        line-height: 19px;
        font-family: 'Inter';

        @media only screen and (max-width: $bp-lg) {
            margin-right: 16px;
        }

        &--gray {
            color: #8E8E8E;
    
        }
    }

    &__socials {
        box-sizing: border-box;
        width: 240px;
        flex-shrink: 0;
        display: block;
        margin-top: auto;

        @media only screen and (max-width: $bp-xxl) {
            width: 200px;
        }

        @media only screen and (max-width: $bp-xl) {
            order: 1;
            width: 50%;
            padding-right: 30px;
            display: flex;
            margin-top: 0;
        }

        @media only screen and (max-width: $bp-lg) {
            padding-right: 0;
            width: 100%;
            margin-bottom: 40px;
        }
    }

    &__socials-header {
        display: flex;
        flex-direction: column;
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 25px;
            margin-bottom: 0;
        }

        span{
            color: #8E8E8E;
            line-height: 150%;
            margin-bottom: 12px;
        }
    }

    &__socials-logo {
        width: 186px;
        height: 23px;
    }

    &__socials-list {
        display: flex;
        flex-wrap: wrap;

        @media only screen and (max-width: $bp-xl) {
            align-items: center;
        }
    }

    &__socials-item {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 32px;
        height: 32px;
        flex-shrink: 0;
        fill: #D6D6D6;
        margin-right: 5px;
        transition: fill 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            margin-right: 0;
        }

        &:hover{
            fill: $blue;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }

    &__socials-icon {
        width: 100%;
        height: 100%;
    }
}