@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.section-events {
    margin-bottom: 245px;

    @media only screen and (max-width: $bp-xl) {
        margin-bottom: 200px;
    }

    @media only screen and (max-width: $bp-lg) {
        margin-bottom: 160px;
    }

    @media only screen and (max-width: $bp-md) {
        margin-bottom: 135px;
    }

    &__header {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: 40px; 
        
        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    &__title {
        text-align: left;
        margin-right: 32px;

        @media only screen and (max-width: $bp-lg) {
            margin-right: 0;
        }
    }

    &__header-badges {
        @media only screen and (max-width: $bp-lg) {
            margin-top: 35px;
        }
    }

    &__list {
        display: block;

        @media only screen and (max-width: $bp-lg) {
            display: flex;
            flex-direction: column;
        }
        
        &::after{
            content: "";
            display: block;
            clear: both;
        }
    }

    &__item {
        width: 100%;
        margin-bottom: 15px;
        width: calc(100% - 268px);
        float: left;

        @media only screen and (max-width: $bp-xl) {
            width: calc(100% - 240px);
        }
        
        @media only screen and (max-width: $bp-lg) {
            width: 100%;
        }
        
        &[data-category="1"]{
            order: 0;
        }
        &[data-category="2"]{
            order: 2;
        }
    }

    &__label{
        display: none;
        align-items: center;
        margin-bottom: 6px;
        font-family: 'Inter';
        font-weight: 600;
        font-size: 14px;
        line-height: 150%;
        color: #000;
        margin-left: auto;
        margin-bottom: 12px;

        &--online{
            padding: 2px 12px;
            justify-content: center;
            text-align: center;
            border-radius: 20px;
            background-color: #00EBBA;
            color: #fff;
        }

        &[data-category="1"]{
            order: 0;
        }
        &[data-category="2"]{
            order: 2;
        }

        @media only screen and (max-width: $bp-lg) {
            display: inline-flex;
        }
    }

    &__tooltip {
        position: relative;
        margin-left: 8px;

        .icon{
            cursor: pointer;
            width: 20px;
            height: 20px;
            flex-shrink: 0;
            fill: #B8B8B8;
        }
    }

    &__tooltip:hover &__tooltip-content{
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }

    &__tooltip-content {
        position: absolute;
        bottom: 50%;
        left: 100%;
        padding: 10px 16px;
        visibility: hidden;
        opacity: 0;
        pointer-events: none;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0px 0px 44px #E1F4FF;
        width: 250px;
        font-size: 14px;
        color: #000;
        line-height: 120%;
        z-index: 1;
        transition: opacity 0.4s ease, visibility 0.4s ease;

        @media only screen and (max-width: $bp-lg) {
            right: 100%;
            left: auto;  
        }
    }

    &__aside {
        position: relative;
        display: block;
        order: 1;
        float: right;
        width: 248px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-xl) {
            width: 220px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin: 64px 0 80px;
            width: 100%;
        }
    }

    &__aside-main {
        position: relative;
        display: flex;
        align-content: center;
        flex-direction: column;
        flex-wrap: nowrap;
        box-shadow: 0px 0px 44px #E1F4FF;
        padding: 36px 16px;
        border-radius: 20px;
        background-color: $blue;
        color: #fff;

        @media only screen and (max-width: $bp-lg) {
            padding: 26px 20px;
            flex-direction: row;
            flex-wrap: wrap;
        }
        
        @media only screen and (max-width: $bp-md) {
            padding: 22px 22px 30px;
        }
    }

    &__aside-label{
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: 'Inter';
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: #fff;
        padding: 8px 24px;
        background-color: #F095BA;
        border-radius: 20px;

        top: 8px;
        right: 20px;
        transform: rotate(6deg);
        padding: 8px 16px;

        @media only screen and (max-width: $bp-xl) {
            right: 0;
        }

        @media only screen and (max-width: $bp-lg) {
            top: 0;
            right: -24px;
            transform: rotate(15deg);
        }
    }

    &__aside-title {
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        letter-spacing: 0.15px;
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 8px;
        }
    }

    &__aside-input {
        width: 100%;

        @media only screen and (max-width: $bp-lg) {
            width: calc(100% - 130px);
        }

        @media only screen and (max-width: $bp-sm) {
            width: calc(100% - 80px);
        }

        .icon{
            width: 30px;
            height: 30px;
        }
    }

    &__aside-button {
        margin-top: 35px;

        @media only screen and (max-width: $bp-lg) {
            margin-left: auto;
            width: 45px;
            height: 45px;
            padding: 0;
        }

        .button{

            &__text{
                @media only screen and (max-width: $bp-lg) {
                    display: none;
                }
            }

            &__icon{
                margin-left: 6px;

                @media only screen and (min-width: $bp-lg + 1) {
                    width: 24px;
                    height: 24px;
                }

                @media only screen and (max-width: $bp-lg) {
                    margin-left: 0;
                }
            }
        }
    }

    &__aside-social-block {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-top: 20px;
        width: 100%;
        justify-content: space-between;
        
        span{
            font-family: "Inter";
            font-size: 14px;
            line-height: 120%;
            color: #AEAEAE;
            margin-right: 25px;
            margin-bottom: 15px;
        }
        
        @media only screen and (max-width: $bp-xl) {
            justify-content: space-between;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-top: 35px;
        }
    }

    &__aside-social { 
        display: flex;
        align-items: center;
        margin-bottom: 15px;
    }

    &__aside-social-item {
        fill: #AEAEAE;
        transition: fill 0.35s ease;

        &:hover{
            fill: $blue;
        }

        .icon{
            width: 34px;
            height: 34px;

            @media only screen and (max-width: $bp-lg) {
                width: 30px;
                height: 30px;
            }
        }
    }
}