@import '@styles/vars.scss';

.list {
    display: block;
    margin: 0;
    padding: 0 0 0 12px;

    &__item {
        position: relative;
        display: inline-flex;
        align-items: flex-start;
        padding-left: 30px;
        margin-bottom: 20px;
        
        &:last-of-type{
            margin-bottom: 0;
        }

        &::after{
            content: "";
            display: inline-flex;
            width: 18px;
            height: 1px;
            background-color: $black;
            position: absolute;
            top: 14px;
            left: 0;
        }
    }

    &--round {
        padding-left: 10px;
    }

    &--round &__item {
        padding-left: 14px;

        &::after{
            width: 4px;
            height: 4px;
            border-radius: 2px;
            top: 10px;
        }
    }
}
