// INTER
@font-face {
    font-family: 'Inter';
    src:    url('@assets-fonts/inter/Inter-Light.woff2') format('woff2'),
            url('@assets-fonts/inter/Inter-Light.woff') format('woff'),
            url('@assets-fonts/inter/Inter-Light.ttf') format('ttf');
    font-weight: 300;
    font-style: normal;
    font-display: swap;
}

