@import '@styles/vars.scss';

.about-advantages {
    padding-top: 35px;

    &__inner {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;

        @media only screen and (max-width: $bp-md) {
            justify-content: flex-start;
            margin: 0 -8px;
            display: block;

            &::after{
                content: "";
                display: block;
                clear: both;
            }
        }
    }

    &__item {
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        flex-basis: 33.33%;
        margin-top: 130px;
        padding: 0 25px;

        
        @media only screen and (max-width: $bp-xl) {
            margin-top: 80px;
            padding: 0 15px;
        }
        
        @media only screen and (max-width: $bp-lg) {
            flex-basis: 50%;
            margin-top: 45px;
        }
        
        @media only screen and (max-width: $bp-md) {
            padding: 0 8px;
            float: left;
            width: 50%;
        }
        

        &:nth-of-type(3n + 2):not(:last-of-type){
            margin-top: 0;

            @media only screen and (max-width: $bp-xxl) {
                margin-top: 0;
            }

            @media only screen and (max-width: $bp-xl) {
                margin-right: auto;
                margin-left: auto;
            }

            @media only screen and (max-width: $bp-lg) {
                margin: 45px 0 0;
            }
        }

        @media only screen and (max-width: $bp-md) {
            &:nth-of-type(2){
                margin-top: 145px !important;
            }

            &:nth-of-type(even){
                float: right;
            }
        }
    }

    &__item-img {
        display: block;
        width: 100px;
        height: 100px;
        margin-bottom: 30px;
        object-fit: contain;

        
        @media only screen and (max-width: $bp-lg) {
            width: 80px;
            height: 80px;
        }
        
        @media only screen and (max-width: $bp-md) {
            width: 68px;
            height: 68px;
        }
    }

    &__item-title {
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            font-size: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
            margin-bottom: 20px;
        }
    }

    &__item-text {
        color: $gray-dark;

        @media only screen and (max-width: $bp-md) {
            font-size: 14px;
        }
    }
}