  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    
    color: $black;

    @include media(from_sm) {
      font-size: 48px;
    }

    @include media(from_m) {
      font-size: 40px;
    }

    @include media(from_l) {
      font-size: 50px;
    }

    &--md{
      font-size: 24px;
      
      @include media(from_sm) {
        font-size: 38px;
      }
      
      @include media(from_m) {
        font-size: 22px;
      }
      
      @include media(from_l) {
        font-size: 30px;
      }
    }
  }
}
