@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";
@import "@styles-mixins/hoverEffects.scss";
button,
a {
    padding: 0;
    background: none;
    border: none;
    outline: none;
}

.button {
    cursor: pointer;
    padding: 0;
    background: none;
    border: none;
    outline: none;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    height: 54px;
    box-sizing: border-box;
    padding: 0 32px;
    border-radius: 50px;
    text-decoration: none;
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    color: #fff;
    fill: #fff;
    background-color: $blue;
    transition: color 0.35s ease, fill 0.35s ease, border-color 0.35s ease, background-color 0.35s ease;

    &__text{
        
    }

    &__icon{
        flex-shrink: 0;
        width: 32px;
        height: 32px;
    }

    &__text + &__icon{
        margin-left: 10px;
    }

    &__icon &__text{
        margin-left: 10px;
    }

    &:hover{
        fill: $blue;
        color: $blue;
        background-color: transparent;
    }


    &--border{
        border: 3px solid $blue;
        color: $blue;
        fill: $blue;
        background-color: transparent;

        &:hover{
            background-color: $blue;
            color: #fff;
            fill: #fff;
        }
    }

    &--white{
        background-color: #fff;
        fill: $blue;
        color: $blue;

        &:hover{
            color: #fff;
            fill: #fff;
            background-color: $blue;
        }
    }

    &--border#{&}--white{
        border-color: #fff;
        fill: #fff;
        color: #fff;
        background-color: transparent;

        &:hover{
            border-color: #fff;
            background-color: #fff;
            color: $blue;
            fill: $blue;
        }
    }

    &--yellow{
        background-color: $yellow;
        color: $blue-dark;
        fill: $blue-dark;
        
        &:hover{
            color: $yellow;
            fill: $yellow;
        }
    }

    &--icon{
        padding: 0;
        width: 54px;
    }

}

.button_type_more {
    display: flex;
    align-items: center;

    height: 32px;
    width: 152px;

    background-repeat: no-repeat;
    background-size: 15px auto;
    background-position: right 8px top 50%;
    background-image: url("data:image/svg+xml,%3Csvg width='15' height='9' viewBox='0 0 15 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7.50017 5.81826L13.0689 0.249512L14.6597 1.84026L7.50017 8.99976L0.340668 1.84026L1.93142 0.249511L7.50017 5.81826Z' fill='%233AB3FE'/%3E%3C/svg%3E%0A");

    font-weight: 500;
    font-size: 16px;
    line-height: 120%;

    color: $blue;

    @include defaultHover();

    @include media(from_sm) {
        height: 48px;
        width: 232px;

        background-size: 24px auto;

        font-size: 26px;
    }

    @include media(from_m) {
        height: 24px;
        width: 112px;

        background-size: 12px auto;

        font-size: 12px;
    }

    @include media(from_l) {
        height: 24px;
        width: 144px;

        background-size: 16px auto;

        font-size: 16px;
    }
}

.button_type_default {
    display: flex;
    align-items: center;
    justify-content: center;

    padding: 16px 32px;
    border: 3px solid $blue-light;
    border-radius: 100px;
    background-color: $blue-light;
    
    font-family: Iter, sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 120%;
    color: #fff;

    @include fillHover(#fff, $blue-light);

    @include media(from_sm) {
        padding: 24px 52px;
        font-size: 26px;
    }

    @include media(from_m) {
        padding: 14px 16px;
        font-size: 16px;
    }

    @include media(from_l) {
        padding: 18px 32px;
        font-size: 20px;
    }
}

.button_type_expand {
    width: 24px;
    height: 24px;

    background-repeat: no-repeat;
    background-size: 16px auto;
    background-position: center;
    background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M16 7.5V8.5H8.53333V16H7.46667V8.5H0V7.5H7.46667V0H8.53333V7.5H16Z' fill='%23A2A2A2'/%3E%3C/svg%3E%0A");

    transition: 0.2s ease-in-out;

    background-size: 20px auto;

    @include defaultHover();

    @include media(from_sm) {
        width: 32px;
        height: 32px;
        background-size: 24px auto;
    }

    @include media(from_m) {
        width: 24px;
        height: 24px;
        background-size: 16px auto;
    }

    @include media(from_l) {
        width: 32px;
        height: 32px;
        background-size: 24px auto;
    }
}

.button_type_close {
    width: 24px;
    height: 24px;

    background-repeat: no-repeat;
    background-size: 20px auto;
    background-position: center;
    background-image: url("data:image/svg+xml,%3Csvg width='30' height='27' viewBox='0 0 30 27' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M21.6287 4.48699L22.5126 5.37087L15.9129 11.9705L22.542 18.5997L21.5992 19.5425L14.9701 12.9133L8.37043 19.513L7.48655 18.6291L14.0862 12.0295L7.45709 5.40034L8.39989 4.45753L15.029 11.0867L21.6287 4.48699Z' fill='%23A2A2A2'/%3E%3C/svg%3E%0A");

    transition: 0.2s ease-in-out;

    @include defaultHover();

    @include media(from_sm) {
        width: 32px;
        height: 32px;
        background-size: 28px auto;
    }

    @include media(from_m) {
        width: 24px;
        height: 24px;
        background-size: 20px auto;
    }

    @include media(from_l) {
        width: 32px;
        height: 32px;
        background-size: 28px auto;
    }
}

.button_type_play {
    width: 40px;
    height: 40px;

    background-repeat: no-repeat;
    background-size: 32px auto;
    background-position: center;
    background-image: url("data:image/svg+xml,%3Csvg width='24' height='27' viewBox='0 0 24 27' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M21.7917 10.3655C24.3287 11.8303 24.3287 15.4921 21.7917 16.9569L5.80833 26.1849C3.2713 27.6496 0.100001 25.8187 0.100001 22.8892L0.100002 4.43322C0.100002 1.5037 3.2713 -0.327251 5.80834 1.13751L21.7917 10.3655Z' fill='white'/%3E%3C/svg%3E%0A");
}
