@import '@styles/vars.scss';

.link{
    display: inline-flex;
    align-items: center;
    text-decoration: none;
    color: $blue;
    fill: $blue;
    transition: color 0.25s ease, fill 0.25s ease;

    .icon{
        width: 24px;
        height: 24px;
    }

    span + .icon{
        margin-left: 8px;
    }

    .icon + span {
        margin-left: 8px;
    }

    &:hover{
        color: $blue-dark;
        fill: $blue-dark
    }
}