.section-questions {
    margin-bottom: 200px;

    @media only screen and (max-width: $bp-xl) {
        margin-bottom: 160px;
    }

    @media only screen and (max-width: $bp-lg) {
        margin-bottom: 130px;
    }

    &__inner {
    }

    &__header {
        display: flex;
        align-items: center;
        margin-bottom: 40px;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 50px;
        }
    }

    &__title{
        margin-right: 90px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 20px;
            margin-right: 0;
        }
    }

    &__button {
        height: 50px;
    }

    &__items {
    }

    &__resolution {
        margin-top: 85px;
    }
}