@import '@styles/vars.scss';

.about-intro {
    background-color: #fff;

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @media only screen and (max-width: $bp-xxl){
            
        }
        
        @media only screen and (max-width: $bp-xl){
            
        }
        
        @media only screen and (max-width: $bp-lg){
            flex-direction: column;
            align-items: flex-start;
        }
        
        @media only screen and (max-width: $bp-md){
            
        }
    }

    &__main {
        padding: 45px 0;
        max-width: 382px;
        margin-right: 60px;

        @media only screen and (max-width: $bp-xxl){
            
        }
        
        @media only screen and (max-width: $bp-xl){
            
        }
        
        @media only screen and (max-width: $bp-lg){
            max-width: 280px;
            margin-right: 0;
            margin-top: -60px;
            padding-top: 0;
            padding-bottom: 50px;
        }
        
        @media only screen and (max-width: $bp-md){
            
        }
    }

    &__title {
        color: #000;
        margin-bottom: 55px;
        
        @media only screen and (max-width: $bp-xl){
            margin-bottom: 42px;
        }
        
        @media only screen and (max-width: $bp-lg){
            margin-bottom: 30px;
            font-size: 40px;
        }

    }

    &__button {
        display: inline-flex;
        align-items: center;
        justify-content: space-between;
        border-radius: 50px;
        background-color: $blue;
        text-decoration: none;
        color: #fff;
        height: 55px;
        padding: 0 6px 0 18px;

        &:hover{
            background-color: $blue-light;
        }
        
        .icon{
            width: 45px;
            height: 45px;
            flex-shrink: 0;
            margin-left: 54px;
        }
    }

    &__subtitle {
        margin-top: 148px;
        color: #A4A4A4;
        max-width: 230px;

        
        @media only screen and (max-width: $bp-xl){
            margin-top: 80px;
        }
        
        @media only screen and (max-width: $bp-lg){
            margin-top: 30px;            
        }
        
        @media only screen and (max-width: $bp-md){
            margin-top: 8px;
            font-size: 12px;
            max-width: 200px;
        }

        span{
            font-weight: 700;
            font-size: 20px;
            color: #000;

            @media only screen and (max-width: $bp-md){
                font-size: 12px;
            }
        }
    }

    &__img {
        display: flex;
        flex-shrink: 0;
        width: 666px;
        height: 685px;

        @media only screen and (max-width: $bp-xxl){
            
        }
        
        @media only screen and (max-width: $bp-xl){
            width: auto;
            height: 430px;
        }
        
        @media only screen and (max-width: $bp-lg){
            width: 100%;
            height: auto;
            max-height: 550px;
            order: -1;
        }
        
        @media only screen and (max-width: $bp-md){
            
        }

        img{
            object-fit: cover;
            width: 100%;
            height: 100%;
        }
    }
}
