@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";
@import "@styles-mixins/hoverEffects.scss";

.contacts {
  &__row {
    display: flex;
    align-items: flex-end;
  }

  &__label {
    display: inline-block;
    margin: 0 0 8px 0;

    font-weight: 700;
    font-size: 18px;
    line-height: 120%;
  }

  &__link {
    text-decoration: none;

    font-family: "Inter", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 120%;

    color: $blue-light;

    @include defaultHover();
  }

  &__text {
    margin: 0;

    font-family: "Inter", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    color: $color_text_secondary;
  }
}
