@import "@styles/vars.scss";

.card-offer {
    position: relative;
    box-sizing: border-box;
    display: flex;
    width: 100%;
    min-height: 485px;
    padding: 36px 50px;
    background-color: $blue;
    border-radius: 20px;
    color: #fff;

    @media only screen and (max-width: $bp-xl){
        padding: 40px 45px 14px;
        min-height: 370px;
    }
 
    @media only screen and (max-width: $bp-lg){
        padding: 38px 40px 40px; 
        min-height: 800px;
    }
    
    @media only screen and (max-width: $bp-md){
        padding: 25px 18px;
        min-height: 496px;
    }

    &__inner {
        display: flex;
        flex-direction: column;
        position: relative;
        z-index: 2;
    }

    &__header {
        margin-bottom: auto;

        @media only screen and (max-width: $bp-lg){
            margin-bottom: 30px;
        }
        
        @media only screen and (max-width: $bp-md){
            margin-bottom: 18px;
        }
    }

    &__type {
        display: inline-flex;
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: #fff;
        opacity: 0.5;
        
        @media only screen and (max-width: $bp-lg){
            font-size: 14px;
            margin-bottom: 4px;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 12px;
        }

        &::after{
            content:"";
            display: inline-flex;
            align-self: flex-start;
            width: 7px;
            height: 7px;
            flex-shrink: 0;
            margin-left: 8px;
            margin-bottom: auto;
            border-radius: 50%;
            background-color: #58FFA5;

            @media only screen and (max-width: $bp-lg){
                margin-left: 5px;
            }
            
            @media only screen and (max-width: $bp-md){
                margin-left: 3px;
            }
        }
    }

    &__title {
        font-size: 45px;
        line-height: 120%;

        @media only screen and (max-width: $bp-xl){
            font-size: 35px;
            margin-bottom: 5px;
        }

        @media only screen and (max-width: $bp-md){
            font-size: 30px;
        }
    }

    &__description {
        font-size: 16px;
        line-height: 120%;

        @media only screen and (max-width: $bp-lg){
            font-size: 14px;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 12px;
        }
    }

    &__body {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        
        @media only screen and (max-width: $bp-lg){
            flex-grow: 1;
        }
    }

    &__prices {
                
        @media only screen and (max-width: $bp-lg){
            margin-bottom: auto;
        }
    }

    &__price-old {
        display: inline-flex;
        font-family: "Inter";
        font-weight: 700;
        font-size: 20px;
        line-height: 120%;
        color: #000;
        position: relative;

        @media only screen and (max-width: $bp-xl){
            font-size: 18px;            
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 16px;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 14px;
        }

        &::after{
            content:"";
            height: 4px;
            width: 100%;
            display: block;
            position: absolute;
            top: 50%;
            transform: translate(0,-50%) rotate(-11deg);
            background-color: #F095BA;
        }
    }

    &__price {
        font-style: normal;
        font-weight: 400;
        font-size: 60px;
        line-height: 120%;
        letter-spacing: 0.15px;

        @media only screen and (max-width: $bp-xl){
            font-size: 45px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 38px;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 30px;
        }
    }

    &__button {
        margin-top: 16px;
    }

    &__subtitle {
        font-size: 14px;
        line-height: 120%;
        color: #000;
        opacity: 0.2;
        margin-top: 40px;

        @media only screen and (max-width: $bp-xl){
            margin-top: 26px;
            font-size: 10px;
        }

        @media only screen and (max-width: $bp-lg){
            display: none;
        }
    }

    &__label{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 134px;
        height: 134px;
        position: absolute;
        bottom: -45px;
        right: 45px;
        transform: rotate(20deg);
        z-index: 1;
        border-radius: 50%;
        flex-shrink: 0;
        background-color: $blue-dark;

        @media only screen and (max-width: $bp-lg){
            width: 110px;
            height: 110px;
            right: -12px;
            bottom: 48px;
        }
        
        img{
            position: absolute;
            top: 12px;
            left: 10px;
            width: 45px;
            height: 56px;

            @media only screen and (max-width: $bp-lg){
                width: 37px;
                height: 47px;
                top: 10px;
                left: 8px;
            }
        }
    }

    &__label-text{
        display: flex;
        align-items: center;
        flex-direction: column;
        font-family: "Inter";
        font-weight: 700;
        font-size: 45px;
        line-height: 120%;
        color: #fff;

        @media only screen and (max-width: $bp-lg){
            font-size: 36px;
        }

        span{
            font-size: 24px;
            line-height: 120%;
            margin-top: -15px;

            @media only screen and (max-width: $bp-xl){
                margin-top: -10px;
            }
            
            @media only screen and (max-width: $bp-lg){
                font-size: 20px;
            }
        }
    }

    &__img{
        width: auto;
        max-width: 100%;
        height: calc(100% + 30px);
        object-fit: contain;
        position: absolute;
        bottom: 0;
        right: 0;

        @media only screen and (max-width: $bp-xl){
            height: calc(100% + 20px);
            object-fit: cover;
        }
        
        @media only screen and (max-width: $bp-lg){
            height: 468px;
            bottom: 40px;
            right: 50%;
            transform: translateX(50%);
        }
        
        @media only screen and (max-width: $bp-md){
            height: 290px;
        }
    }
}