@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";
@import "@styles-mixins/hoverEffects.scss";

.section-specialist {
    margin-bottom: 190px;

    @media only screen and (max-width: $bp-xl) {
        margin-bottom: 160px;
    }

    @media only screen and (max-width: $bp-lg) {
        margin-bottom: 130px;
    }

    &__header {
        margin-bottom: 35px;
    }

    &__link {
        display: none;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 12px;
            display: inline-flex;
        }
    }

    &__slider {
        
        .slider {

            &__inner {
                overflow: hidden;
                border-radius: 20px;
                margin: 0 -35px;
                padding: 0 35px;
            }

            &__navigation {
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                margin: 0 -35px;
                min-width: 100%;
                justify-content: space-between;
            }

            &__button {
                width: 70px;
                height: 70px;
                fill: #fff;
                background-color: $blue;
                padding: 6px;
                border: none;

                @media only screen and (max-width: $bp-lg) {
                    display: none;
                }

                &:hover{
                    background-color: $blue-dark;
                }

                &--prev {
                    justify-content: flex-start;
                }

                &--next {
                    justify-content: flex-end;
                }
            }

            &__button-icon {
                width: 32px;
                height: 32px;
            }

            &__pagination {
                display: none;

                @media only screen and (max-width: $bp-lg) {
                    display: flex;
                }

                @media only screen and (max-width: $bp-md) {
                    margin-top: 20px;
                }
            }
        }

    }

    &__features {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin: 45px -5px -50px;
    }

    &__feature {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        padding: 0 5px;
        width: 33.33%;
        margin-bottom: 50px;

        @media only screen and (max-width: $bp-xl) {
            width: 50%;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
        }
    }

    &__feature-icon {
        flex-shrink: 0;
        width: 110px;
        height: 89px;
        margin-right: 20px;

        @media only screen and (max-width: $bp-lg) {
            width: 98px;
            height: 79px;
        }
    }

    &__feature-title {

        @media only screen and (max-width: $bp-lg) {
            font-size: 18px;
        }
    }

    &__tabs {
        display: flex;
        flex-wrap: wrap;
        margin: 45px -20px -10px;

        @media only screen and (max-width: $bp-xl) {
            margin: 35px -15px -10px;
        }

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__tab-link {
        font-family: 'Inter';
        margin: 0 20px 10px;
        text-align: left;
        width: calc(100% / 6 - 40px);
        

        @media only screen and (max-width: $bp-xxl) {
            width: calc(100% / 5 - 40px);
        }
        
        @media only screen and (max-width: $bp-xl) {
            width: calc(100% / 4 - 30px);
            margin: 0 15px 10px;
        }

        &.is-active{
            cursor: default;
            pointer-events: none;
            color: $black;
        }
    }
}

.card-specialist {
    box-sizing: border-box;
    min-height: 436px;
    border-radius: 20px;

    &__inner {
        display: flex;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
        }
    }

    &__img-wrapper {
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        width: 400px;
        min-height: 100%;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-xl) {
            width: 350px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            min-height: auto;
            height: auto;
            height: 240px;
        }

        @media only screen and (max-width: $bp-md) {
            height: 148px;
        }
    }

    &__img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 20px 0 0 20px;
        object-position: center;

        @media only screen and (max-width: $bp-lg) {
            border-radius: 20px 20px 0 0;
        }

    }

    &__video-link{
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        width: 96px;
        height: 96px;
        bottom: 40px;
        right: 0;
        transform: translateX(50%);

        @media only screen and (max-width: $bp-lg) {
            width: 75px;
            height: 75px;
            bottom: 0;
            transform: translate(8px, 40%);
        }

        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }

    &__main{
        display: flex;
        flex-direction: column;
        background-color: #fff;
        border-radius: 0 20px 20px 0;
        padding: 25px 150px 25px 85px;

        @media only screen and (max-width: $bp-xxl) {
            padding: 25px 60px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding: 20px 32px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 12px 16px 20px;
        }
    }

    &__title {
        margin-bottom: 5px;
        
        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 0;
        }
    }

    &__role {
        margin-bottom: 14px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 6px;
        }
    }

    &__text {
        line-height: 150%;
        color: #666;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-bottom: 10px;
        }
    }

    &__regalia {
        display: flex;
        align-items: center;
        background: rgba(58, 179, 254, 0.1);
        border-radius: 10px;
        padding: 16px 32px;

        @media only screen and (max-width: $bp-lg) {
            padding: 12px 24px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 10px 16px;
        }
    }

    &__regialia-img {
        width: 64px;
        height: 64px;
        margin-right: 24px;
        flex-shrink: 0;
        
        @media only screen and (max-width: $bp-lg) {
            width: 68px;
            height: 68px;
            margin-right: 12px;
        }
    }

    &__regalia-text {
        color: #666;
    }

    &__link {
        margin-top: 20px;
        margin-left: auto;

        @media only screen and (max-width: $bp-lg) {
            margin-left: 0;
        }
    }
}
