@import '@styles/vars.scss';

.teachers-menu {
    width: 375px;

    &__number {
        font-size: 130px;
        line-height: 120%;
        color: $blue;
        font-family: 'Inter';
        font-weight: 900;
    }

    &__title {
        margin-bottom: 45px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 35px;
        }
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: -10px;
    }

    &__list-item {
        display: flex;
        flex-basis: calc(50% - 10px);
        margin-right: 20px;
        text-decoration: none;
        color: $blue;
        margin-bottom: 10px;
        transition: color 0.35s ease;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 20px;
        }

        &:hover{
            color: $blue-light;
        }

        &:nth-of-type(even){
            margin-right: 0;
        }
    }
}