@import '@styles/vars.scss';

.pagination {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin-top: 42px;

    @media only screen and (max-width: $bp-lg) {
        margin-top: 35px;
    }

    &__list {
        display: flex;
        align-items: center;

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__item {
        display: inline-flex;
        align-items: center;
        text-decoration: none;
        font-family: 'Inter';
        color: #B9B9B9;
        font-size: 14px;
        line-height: 24px;
        padding: 0 6px;
        height: 100%;
        transition: font-size 0.15s ease, color 0.35s ease;

        &:hover{
            color: $blue;
        }


        &::after{
            content:"";
            pointer-events: none;
            display: inline-flex;
            margin-left: 0px;
            height: 1px;
            width: 0px;
            background-color: #000;
            opacity: 0.2;
            transition: margin 0.45s ease, width 0.45s ease;
        }

        &.is-active{
            font-size: 20px;
            color: $blue;

            &::after{
                width: 50px;
                margin-left: 20px;
            }
        }
    }

    &__item-icon{
        display: flex;
        align-items: center;
        justify-content: center;
        width: 32px;
        height: 32px;
        fill: $blue;
        transition: fill 0.35s ease;

                
        &:first-of-type{
            margin-right: 44px;

            @media only screen and (max-width: $bp-xl) {
                margin-right: 18px;
            }
        }

        &:last-of-type{
            margin-left: 44px;

            @media only screen and (max-width: $bp-xl) {
                margin-left: 18px;
            }
        }


        &:hover{
            fill: $blue-dark;
        }

        .icon{
            width: 100%;
            height: 100%;
        }
    }

    &__list + &__button{
        margin-right: 0;

        @media only screen and (max-width: $bp-lg) {
            margin-right: auto;
        }
    }

    &__button {
       margin: 0 auto;
       
       @media only screen and (max-width: $bp-lg) {
           height: 44px;
       }
    }
}
