@import "@styles/vars.scss";

.section-info{
    padding: 80px 0;

    @media only screen and (max-width: $bp-lg) {
        padding: 60px 0;
    }

    &__inner{
        padding: 100px 80px;

        @media only screen and (max-width: $bp-xl) {
            padding: 75px 50px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding: 50px 0;
        }
    }

    &__text{
        font-weight: 400;
        line-height: 150%;
        color: #000;
        text-align: center;
        margin-bottom: 20px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 15px;
        }

        &:last-of-type{
            margin-bottom: 0;
        }

        span{
            font-weight: 700;
        }
    }

    &--border &__inner{
        border: 2px solid #C9EBFF;
        border-width: 2px 0;
    }
}