@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";

.card-event {
    display: block;
    box-sizing: border-box;
    background-color: #fff;
    border-radius: 20px;
    padding: 18px 16px 18px 24px;

    @media only screen and (max-width: $bp-lg) {
        padding: 18px 22px;
    }
    
    &__inner {
        display: flex;
        flex-wrap: wrap;
        align-items: center; 
        margin-bottom: -30px;

        &>*{
            margin-bottom: 30px;
        }
    }

    &__date {
        text-align: center;
        display: flex;
        flex-direction: column;
        font-family: 'Inter';
        font-weight: 300;
        font-size: 45px;
        line-height: 120%;
        color: #CCCCCC;
        margin-right: 32px;

        @media only screen and (max-width: $bp-lg) {
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: 16px;
        }

        span{
            font-weight: 600;
            font-size: 24px;
            line-height: 120%;
            margin-top: -12px;
        }
    }

    &__info {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        flex: 0 1 42%;

        @media only screen and (max-width: $bp-xl) {
            flex: 1 1 70%;
        }
    }

    &__label {
        display: inline-flex;
        align-items: center;
        margin-bottom: 6px;
        font-family: 'Inter';
        font-weight: 600;
        font-size: 14px;
        line-height: 150%;
        color: #000;

        &--online{
            padding: 2px 12px;
            display: inline-flex;
            align-items: center;
            justify-content: center;
            text-align: center;
            border-radius: 20px;
            background-color: #00EBBA;
            color: #fff;
        }
        
        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }

    &__tooltip {
        display: flex;
        align-items: center;
        position: relative;
        margin-left: 8px;

        .icon{
            cursor: pointer;
            width: 20px;
            height: 20px;
            flex-shrink: 0;
            fill: #B8B8B8;
        }
    }

    &__tooltip:hover &__tooltip-content{
        opacity: 1;
        visibility: visible;
        pointer-events: all;
    }

    &__tooltip-content {
        position: absolute;
        bottom: 50%;
        left: 100%;
        padding: 10px 16px;
        visibility: hidden;
        opacity: 0;
        pointer-events: none;
        background-color: #fff;
        border-radius: 10px;
        box-shadow: 0px 0px 44px #E1F4FF;
        width: 250px;
        font-size: 14px;
        color: #000;
        line-height: 120%;
        z-index: 1;
        transition: opacity 0.4s ease, visibility 0.4s ease;

        @media only screen and (max-width: $bp-lg) {
            right: 100%;
            left: auto;
        }
    }

    &__title {
        text-decoration: none;
        font-weight: 700;
        font-size: 18px;
        line-height: 120%;
        color: $blue;
        margin-right: 30px;

        @media only screen and (max-width: $bp-lg) {
            margin-right: 20px;
            font-size: 16px;
        }

        @media only screen and (max-width: $bp-md) {
            margin-right: 0;
        }
    }

    &__price-block {
        flex: 1 1 auto;

        @media only screen and (max-width: $bp-xl) {
            margin-right: auto;
            flex-basis: 50%;
        }
    }

    &__price {
        position: relative;
        font-family: 'Inter';
        font-size: 30px;
        line-height: 120%;
        color: #000;

        @media only screen and (max-width: $bp-lg) {
            font-size: 25px;
            color: rgba(0, 0, 0, 0.5);
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 22px;
        }

        &--old{
            color: #D9D9D9;
        }
    }

    &__price-label {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateX(25%) rotate(-6deg);
        padding: 6px 14px;
        font-family: 'Inter';
        font-style: normal;
        font-weight: 700;
        font-size: 16px;
        line-height: 120%;
        color: #fff;
        background-color: #F095BA;
        border-radius: 20px;
    }

    &__button {

        @media only screen and (max-width: $bp-lg) {
            height: 36px;
        }

        .icon{
            display: flex;

            @media only screen and (max-width: $bp-lg) {
                display: none;
            }
        }
    }
}