@import "@styles/vars.scss";

.section-path{
    position: relative;
    padding-top: 134px;
    color: #000;

    @media only screen and (max-width: $bp-xxl){
        padding-top: 118px;
    }
    
    @media only screen and (max-width: $bp-xl){
        padding-top: 55px;
    }
    
    @media only screen and (max-width: $bp-md){
        padding-top: 98px;
    }

    &__container{
        padding: 0 37.5px;
        position: relative;

        @media only screen and (max-width: $bp-md){
            padding: 0 16px;
        }
    }

    &__inner{
        z-index: 3;
    }

    &__title{
        text-align: center;
        font-weight: 700;
        font-size: 60px;
        line-height: 110%;

        @media only screen and (max-width: $bp-xl){
            font-size: 45px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 47px;
            text-align: left;
            margin-left: 45px;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 35px;
            margin-left: 20px;
        }
    }

    &__content{
        position: relative;
        display: block;
        height: 1440px;
        margin-top: -28px;

        @media only screen and (max-width: $bp-xl){
            height: 1060px;
        }

        @media only screen and (max-width: $bp-lg){
            height: 1975px;
            margin-top: 105px;
        }

        @media only screen and (max-width: $bp-md){
            height: 1458px;
            margin-top: 75px;
        }
    }

    &__stages {
    }

    &__stage {
        position: absolute;
        display: flex;
        color: #000;
        max-width: 368px;
        z-index: 3;

        @media only screen and (max-width: $bp-lg){
            flex-direction: column;
        }

        &--1{
            top: 124px;
            left: 55px;

            @media only screen and (max-width: $bp-xl){
                max-width: 278px;
                top: 90px;
                left: 75px;
            }

            @media only screen and (max-width: $bp-lg){
                top: -60px;
                right: 180px;
                left: auto;
            }

            @media only screen and (max-width: $bp-md){
                top: -40px;
                right: 50px;
            }

            @media only screen and (max-width: $bp-sm){
                width: 208px;
                // right: 0;
            }
        }

        &--2{
            top: 370px;
            left: 50%;
            transform: translateX(-115px);
            max-width: 315px;

            @media only screen and (max-width: $bp-xl){
                max-width: 230px;
                top: 280px;
            }

            @media only screen and (max-width: $bp-lg){
                top: 250px;
                right: 280px;
                left: auto;
                transform: none;
            }

            @media only screen and (max-width: $bp-md){
                top: 192px;
                right: auto;
                left: 20px;
                width: 190px;
            }
        }

        &--3{
            max-width: 300px;
            top: 90px;
            right: 240px;
            
            @media only screen and (max-width: $bp-xl){
                right: 180px;
                max-width: 235px;
            }

            @media only screen and (max-width: $bp-lg){
                width: 280px;
                right: 200px;
                top: 940px;
            }

            @media only screen and (max-width: $bp-md){
                top: 710px;
                width: 208px;
                right: 130px;
            }

            @media only screen and (max-width: $bp-sm){
                right: 30px;
            }
        }
    }

    &__stage-number {
        font-family: 'Inter';
        font-weight: 300;
        font-size: 30px;
        line-height: 36px;
        margin-right: 20px;

        @media only screen and (max-width: $bp-xl){
            font-size: 22px;
            margin-right: 10px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 18px;
            margin-right: 0;
            margin-bottom: 6px;
            line-height: 150%;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 14px;
            margin-bottom: 8px;
        }
    }

    &__stage-title {
        font-weight: 700;
        font-size: 25px;
        line-height: 110%;
        margin-bottom: 24px;

        @media only screen and (max-width: $bp-xl){
            font-size: 19px;
            margin-bottom: 18px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 27px;
            margin-bottom: 12px;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 20px;
            margin-bottom: 8px;
        }
    }

    &__stage-text {
        font-family: 'Inter';
        font-weight: 400;
        font-size: 16px;
        line-height: 150%;
        color: #888;

        @media only screen and (max-width: $bp-xl){
            font-size: 12px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 19px;
            -webkit-line-clamp: 3;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            overflow: hidden;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 14px;
        }
    }

    &__logo {
        width: 344px;
        height: 44px;
        position: absolute;
        top: 365px;
        right: 0;
        z-index: 3;

        @media only screen and (max-width: $bp-xl){
            width: 212px;
            height: 27px;
            top: 280px;
        }
        
        @media only screen and (max-width: $bp-lg){
            width: 216px;
            height: 28px;
            left: 100px;
            top: 540px;
        }
        
        @media only screen and (max-width: $bp-md){
            width: 160px;
            height: 21px;
            top: 410px;
            right: auto;
            left: 0;
        }
    }

    &__courses {
        @media only screen and (max-width: $bp-lg){
            display: block;
            position: absolute;
            right: 70px;
            top: 1340px;
            width: 390px;
        }

        @media only screen and (max-width: $bp-md){
            top: 1010px;
            width: 290px;
            right: 50px;
        }

        @media only screen and (max-width: $bp-sm){
            right: -10px;
        }
    }

    &__courses-title {
        position: absolute;
        text-align: center;
        font-weight: 700;
        font-size: 50px;
        line-height: 100%;
        top: 908px;
        left: 50%;
        transform: translateX(-50%);

        @media only screen and (max-width: $bp-xl){
            font-size: 38px;
            top: 668px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 54px;
            text-align: left;
            position: static;
            margin-bottom: 95px;
            transform: none;

            br{
                display: none;
            }
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 40px;
            margin-bottom: 40px;
        }
    }

    &__course {
        position: absolute;
        display: flex;
        text-decoration: none;
        color: #000;
        z-index: 3;

        @media only screen and (max-width: $bp-lg){
            align-items: center;
            position: static;
            margin-bottom: 56px;

            &:last-of-type{
                margin-bottom: 0;
            }
        }

        @media only screen and (max-width: $bp-md){
            margin-bottom: 36px;
        }

        &--1{
            top: 718px;
            left: 0;
            max-width: 466px;

            @media only screen and (max-width: $bp-xl){
                top: 465px;
            }
        }

        &--2{
            top: 778px;
            right: 0px;
            max-width: 412px;

            @media only screen and (max-width: $bp-xl){
                top: 590px;
                max-width: 306px;
            }
        }

        &--3{
            top: 995px;
            left: 0;
            max-width: 418px;

            @media only screen and (max-width: $bp-xl){
                top: 745px;
                max-width: 310px;
            }
        }

        @media only screen and (max-width: $bp-lg){
            max-width: none
        }
    }

    &__course-img {
        width: 110px;
        height: 110px;
        flex-shrink: 0;
        margin-right: 16px;

        @media only screen and (max-width: $bp-xl){
            width: 70px;
            height: 70px;
            margin-right: 18px;
        }
        
        @media only screen and (max-width: $bp-lg){
            width: 80px;
            height: 80px;
            margin-right: 30px;
        }
        
        @media only screen and (max-width: $bp-md){
            width: 60px;
            height: 60px;
            margin-right: 24px;
        }
    }

    &__course-main {
    }

    &__course-title {
        font-size: 30px;
        line-height: 120%;
        margin-bottom: 16px;

        @media only screen and (max-width: $bp-xl){
            font-size: 22px;
        }
        
        @media only screen and (max-width: $bp-lg){
            font-size: 27px;
            margin-bottom: 0;
        }
        
        @media only screen and (max-width: $bp-md){
            font-size: 20px;
        }
    }

    &__course-labels {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        margin-bottom: -10px;

                
        @media only screen and (max-width: $bp-lg){
            display: none;
        }
    }

    &__course-label {
        padding: 0 16px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        background-color: $blue;
        color: #fff;
        font-size: 16px;
        line-height: 19px;
        margin-right: 12px;
        margin-bottom: 10px;
        height: 35px;
        border-radius: 20px;
        
        @media only screen and (max-width: $bp-xl){
            font-size: 12px;
            height: 24px;
            padding: 0 12px;
        }

        
        @media only screen and (max-width: $bp-md){
            
        }
        
        &:last-of-type{
            margin-right: 0;
        }
    }

    &__bg{
        pointer-events: none;
        position: absolute;
        width: auto;
        height: 100%;
        top: 0;
        left: 50%;
        transform: translateX(-50%);

        @media only screen and (max-width: $bp-lg){
            right: 100px;
            left: auto;
            transform: none;
        }

        @media only screen and (max-width: $bp-sm){
            right: -10px;
        }

        img{
            height: 100%;
            width: auto;
        }
    }
}
