@import '@styles/vars.scss';

.filter {
    width: 100%;
    &__header {
        display: none;

        @media only screen and (max-width: $bp-lg) {
            display: flex;
            align-items: center;
            justify-content: flex-end;
        }
    }

    &__trigger {
        cursor: pointer;
        display: inline-flex;
        align-items: flex-start;
        fill: #062125;
        position: relative;
    }

    &__trigger-label {
        position: relative;
        top: 0;
        display: inline-flex;
        justify-content: center;
        border-radius: 20px;
        height: 20px;
        padding: 0 9px;
        font-size: 16px;
        line-height: 100%;
        font-weight: 500;
        color: #fff;
        background-color: #F095BA;
        z-index: 1;
        margin-right: -8px;
        margin-top: -5px;
        span{
            max-width: 3ch;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
        }
    }

    &__trigger-icon {
        width: 24px;
        height: 24px;
    }

    &__content {

        @media only screen and (max-width: $bp-lg) {
            box-sizing: border-box;
            display: none;
            flex-direction: column;
            align-items: center;
            top: 80px;
            position: fixed;
            left: 0;
            width: 100%;
            z-index: 20;
            background-color: #F1FAFF;
            padding: 14px 14px 50px;
            overflow: hidden hidden;
            max-height: calc(100% - 80px);
            display: flex;
            pointer-events: none;
            visibility: hidden;
            opacity: 0;
            transform: translateY(-15px);
            transition: opacity 0.5s ease, visibility 0.5s ease, transform 0.35s ease;

            &.is-active{
                visibility: visible;
                opacity: 1;
                pointer-events: all;
                transform: translateY(0);
            }
        }
    }

    &__close {
        display: none;
        cursor: pointer;
        flex-shrink: 0;
        width: 24px;
        height: 24px;
        margin-bottom: 20px;
        align-items: center;
        justify-content: center;
        margin-left: auto;
        transition: fill 0.35s ease;

        &:hover{
            fill: $blue;
        }

        @media only screen and (max-width: $bp-lg) {
            display: flex;
        }
    }

    &__badges {
        width: 100%;
        justify-content: space-between;
        margin-bottom: 30px;

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 20px;
            flex-shrink: 0;
        }
    }

    &__list {
        width: 100%;

        @media only screen and (max-width: $bp-lg) {
            overflow-y: auto;
        }
    }

    &__group {
        padding-bottom: 8px;
        border-bottom: 1px solid #C1C8CC;
        margin-bottom: 4px;

        &:last-of-type{
            margin-bottom: 0;
            border-bottom: none;
        }
    }
    
    &__group.is-active &__group-icon{
        transform: rotate(90deg);
    }
    
    &__group.is-active &__group-body{
        opacity: 1;
    }

    &__group.is-active &__group-header{
        padding-bottom: 20px;
    }

    &__group-header {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 10px 18px 10px 24px;
        transition: padding 0.35s ease;

        &:hover{
            padding-bottom: 20px;
        }

        @media only screen and (max-width: $bp-lg) {
            padding-left: 22px;
        }
    }

    &__group-header:hover &__group-icon{
        fill: $blue;
    }

    &__group-title {

        @media only screen and (max-width: $bp-lg) {
            color: $blue;
            font-weight: 600;
        }
    }

    &__group-icon {
        width: 26px;
        height: 26px;
        margin-left: 24px;
        fill: #000;
        transition: transform 0.35s ease, fill 0.35s ease;
    }

    &__group-body{
        opacity: 0;
        max-height: 0;
        overflow: hidden;
        transition: max-height 0.5s ease, opacity 0.4s ease;
    }

    &__group-list {
        padding: 0 18px 26px 35px;

        @media only screen and (max-width: $bp-lg) {
            padding-left: 24px;
        }
    }

    &__checkbox {

    }

    &__radio {
        
    }

    &__checkbox,
    &__radio {
        margin-bottom: 10px;
        
        &:last-of-type{
            margin-bottom: 0;
        }
    }
    
    &__privileges-block {
        width: 100%;
        box-sizing: border-box;
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: #fff;
        border-radius: 20px;
        margin-top: 30px;
        padding: 22px 35px 22px 25px;

        @media only screen and (max-width: $bp-lg) {
            margin-top: 20px;
            margin-top: 0;
        }
    }

    &__privileges-title-wrapper {
        display: flex;
        align-items: flex-start;
        max-width: 185px;
    }

    &__privileges-title {
    }

    &__privileges-tooltip {
        margin-left: 4px;
        fill: #000;

        .tooltip{
            &__icon{
                width: 20px;
                height: 20px;
            }
        }
    }

    &__privileges-tumbler {
        margin-left: 20px;
    }

    &__actions {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-top: 30px;
        flex-shrink: 0;

        @media only screen and (max-width: $bp-lg) {
            justify-content: space-around;
        }
    }

    &__action-button {
        @media only screen and (max-width: $bp-lg) {
            height: 44px;
        }
    }

    
    &__action-clear {
        height: 100%;
        font-weight: 500;
        color: #F095BA;
        padding: 0 20px;
        margin-right: 4px;

        &:hover{
            color: #ff2d4c;
        }
    }

    &__actions &__action-clear{
        height: 54px;

        @media only screen and (max-width: $bp-lg) {
            height: 44px;
        }
    }
}
