@import '@styles/vars.scss';
@import '@styles-mixins/media.scss';
@import '@styles-mixins/hoverEffects.scss';

.video {
  overflow: hidden;

  &__pic-wrapper {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 100%;
    height: 100%;

    background-color: #3AB3FE;
    overflow: hidden;
    transition: 0.2s ease-in-out;

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      z-index: 1;

      width: 100%;  
      height: 100%;

      background: linear-gradient(180deg, rgba(57, 184, 255, 0) 0%, #39B8FF 100%);
    }
  }

  &__pic {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__button {
    cursor: pointer;
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 4;
    transform: translate(-50%, -50%);
    transition: 0.2s ease-in-out;
    
    @include defaultHover();

    .icon{
      width: 48px;
      height: 48px;
    }
  }
}

.video_loaded {
  .video {
    &__pic-wrapper {
      display: none;
      animation: fadeOut 0.2s;
    }
  }
}

.video_playing {
  .video {
    &__pic-wrapper {
      display: none;
      animation: fadeOut 0.2s;
    }

    &__button {
      display: none;
      animation: fadeOut 0.2s;
    }
  }
}

.video_paused {
  .video {
    &__pic-wrapper {
      display: block;
      animation: fadeIn 0.2s;
    }

    &__button {
      display: block;
      animation: fadeIn 0.2s;
    }
  }
}
