@import "@styles/vars.scss";
@import "@styles/animations.scss";
@import "@styles-mixins/media.scss";
@import "@styles-mixins/hoverEffects.scss";

.header {
    padding: 16px 0;
    position: sticky;
    top: 0;
    z-index: 50;
    background-color: #fff;

    @media only screen and (max-width: $bp-xl) {
        padding: 20px 0;
    }

    &.is-active .logo{
        width: 168px;
    }

    &__container {
        box-sizing: border-box;
        max-width: 1770px;
        padding: 8px 16px;
        padding: 0 16px;

        @media only screen and (max-width: $bp-xxl) {
            padding: 0 32px;
        }

        @media only screen and (max-width: $bp-xl) {
            padding: 0 20px;
        }
    }

    &__inner{
        display: flex;
        align-items: center;
    }

    &__logo {
        display: flex;
        height: 42px;
        margin-right: 85px;

        @media only screen and (max-width: $bp-xxl) {
            margin-right: 40px;
        }

        @media only screen and (max-width: $bp-xl) {
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            height: 40px;
            margin-right: 16px;
            overflow: hidden;
            width: 45px;
            transition: width 0.4s ease;
        }

        @media only screen and (max-width: $bp-md) {
            height: 38px;
            width: 72px;

        }

        img{
            flex-shrink: 0;
            height: 100%;
            width: auto;
        }

        .logo{
            &__pic {
                width: 72px;

                @media only screen and (max-width: $bp-lg) {
                    width: 45px;
                }

                @media only screen and (max-width: $bp-md) {
                    width: 72px;
                }
            }
            
            &__label {
                width: 102px;
                margin-left: 8px;

                @media only screen and (max-width: $bp-lg) {
                    width: 80px;
                }
            }
        }
    }

    &__button{
        height: 40px;
        margin-right: 32px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin: 0 auto;
        }
    }

    &__link-title {
    }

    &__burger {
        cursor: pointer;
        position: relative;
        display: none;
        align-items: center;
        justify-content: center;
        width: 26px;
        height: 18px;
        padding: 0;
        margin: 0 0 0 16px;
        background: none;
        border: none;

        @media only screen and (max-width: $bp-lg) {
            display: flex;
        }

        span, 
        &::before,
        &::after{
            display: flex;
            width: 25px;
            height: 3px;
            border-radius: 2px;
            background-color: #CECECE;
            position: absolute;
            top: 50%;
            left: 50%;
        }

        span{
            transform: translate(-50%, -50%);
            transition: opacity 0.35s ease, background-color 0.35s ease;
        }

        &::after{
            content: "";
            transform: translate(-50%, 6px);
            transition: transform 0.35s ease, background-color 0.35s ease;
        }

        &::before{
            content: "";
            transform: translate(-50%, -9px);
            transition: transform 0.35s ease, background-color 0.35s ease;
        }

        &.is-active{
            span{
                background-color: #000;
                opacity: 0;
            }

            &::after{
                background-color: #000;
                transform: translate(-50%, -1.5px) rotate(45deg);
            }
    
            &::before{
                background-color: #000;
                transform: translate(-50%, -1.5px) rotate(-45deg);
            }
        }

        &:hover{
            span,
            &::before,
            &::after{
                background-color: $blue;
            }
        }
    }

    &__menu {
        box-sizing: border-box;
        display: flex;
        align-items: center;
        flex-grow: 1;
        transition: visible 0.5s ease, opacity 0.5s ease, transform 0.5s ease;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            width: 100%;
            position: absolute;
            top: 100%;
            right: 0;
            background-color: #fff;
            padding: 10px 0 45px;
            opacity: 0;
            pointer-events: none;
            transform: translateY(-25%);
            z-index: -2;
            &>*{
                opacity: 0;
                transition: opacity 0.24s ease;
            }
        }

        &.is-active{
            pointer-events: all;
            opacity: 1;
            transform: translateY(0);
            &>*{
                opacity: 1;
                transition: opacity 0.25s ease 0.15s;
            }
        }
    }

    &__nav-wrapper {
        box-sizing: border-box;
        display: flex;

        @media only screen and (max-width: $bp-lg) {
            width: 100%;
            justify-content: flex-end;
            margin-left: auto;
            background-repeat: no-repeat;
            background-size: 220px auto;
            background-image: url(../../assets/media/header/menu-pic.svg);
            padding: 35px 50px 60px;
            border-bottom: 2px solid #dff3fe;
        }

        @media only screen and (max-width: $bp-md) {
            background-size: 164px auto;
            
        }

    }

    &__nav {
        display: flex;
        align-items: center;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            align-items: flex-end;
        }

    }

    &__nav-link {
        font-family: 'Inter';
        margin-right: 32px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 12px;
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-bottom: 25px;
            margin-right: 0;
            text-align: right;
        }

        &:last-of-type{
            margin-right: 0;
            margin-bottom: 0;
        }

        &--hidden{
            display: none;

            @media only screen and (max-width: $bp-lg) {
                display: inline-flex;
            }
        }
    }

    &__contacts {
        order: 2;

        @media only screen and (max-width: $bp-lg) {
            padding: 0 24px;
            margin-top: 50px;
            width: 100%;
            box-sizing: border-box;

        }

        .contacts {

            &__row {
                display: flex;
                align-items: center;
            }
    
            &__label {
                display: none;
                margin-right: 24px;

                @media only screen and (max-width: $bp-lg) {
                    display: inline-flex;
                    margin-bottom: 0;
                }

                @media only screen and (max-width: $bp-md) {
                    font-size: 14px;
                }
            }
    
            &__link {
                font-family: 'Inter';
                color: #000;
                transition: color 0.35s ease;

                @media only screen and (max-width: $bp-xl) {
                    font-size: 12px;
                }

                @media only screen and (max-width: $bp-lg) {
                    font-size: 16px;
                    color: $blue;

                    &:hover{
                        color: $blue-light;
                    }
                }

                &:hover{
                    color: $blue;
                }
            }
        }
    }

    &__socials {
        display: flex;
        flex-wrap: wrap;
        box-sizing: border-box;
        margin-left: 14px;

        @media only screen and (max-width: $bp-lg) {
            align-items: flex-end;
            width: 100%;
            padding: 0 26px;
            margin-top: 30px;
            margin-left: 0;
            order: 2;
        }

        .socials {

            @media only screen and (max-width: $bp-lg) {
               
            }

            &__partner {
                display: none;

                @media only screen and (max-width: $bp-lg) {
                    display: flex;
                    flex-direction: column;
                    margin-right: 20px;
                }
            }
        
            &__partner-label {

                @media only screen and (max-width: $bp-lg) {
                    font-size: 12px;
                    margin-bottom: 12px;
                }
            }
        
            &__partner-logo {
                width: 128px;
                height: 23px;
            }
        
            &__media {

                @media only screen and (max-width: $bp-lg) {
                    margin-top: 15px;
                }
            }
        
            &__item {
                width: 24px;
                height: 24px;
                margin-right: 5px;
                fill: $blue;
                transition: fill 0.35s ease;

                @media only screen and (max-width: $bp-lg) {
                    width: 32px;
                    height: 32px;
                    fill: #D6D6D6;
                }

                &:hover{
                    fill: $blue-dark;
                }
                
                &:last-of-type{
                    margin-right: 0;
                }
            }
        
            &__item-icon {
                width: 100%;
                height: 100%;
            }
        }
    }

    &__login {
        margin-left: auto;
        margin-right: 48px;
        font-size: 14px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 24px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-top: 50px;
        }

        .icon{
            width: 20px;
            height: 20px;
        }

        @media only screen and (max-width: $bp-lg) {
            display: none;
        }
    }
}