@import '@styles/vars.scss';

.about-cooperation {
    padding-top: 142px;

    @media only screen and (max-width: $bp-xl) {
        padding-top: 128px;
    }
    
    @media only screen and (max-width: $bp-lg) {
        padding-top: 116px;
    }
  
    &__main {
        display: flex;
        padding: 70px;
        border-radius: 20px;
        background-color: #fff;

        @media only screen and (max-width: $bp-xl) {
            padding: 50px 45px;
            flex-direction: column;
        }

        @media only screen and (max-width: $bp-lg) {
            padding: 40px 30px;
        }

        @media only screen and (max-width: $bp-md) {
            padding: 30px 16px;
        }
    }

    &__title {
        font-weight: 400;
        width: 480px;
        flex-shrink: 0;
        margin-right: 45px;

        @media only screen and (max-width: $bp-xl) {
            margin-right: 0;
            margin-bottom: 25px;
            width: 100%;
        }

        @media only screen and (max-width: $bp-lg) {
            font-size: 20px;
        }

        @media only screen and (max-width: $bp-md) {
            font-size: 18px;
        }

        @media only screen and (max-width: $bp-sm) {
            font-size: 16px;
        }

        span{
            font-weight: 700;
        }
    }

    &__logos {
        display: flex;
        flex-direction: column;
    }

    &__img {
        width: auto;
        max-width: 100%;
        height: 100px;
        margin-bottom: 45px;
        object-fit: contain;
        object-position: left top;

        @media only screen and (max-width: $bp-xl) {
            margin-bottom: 30px;
        }

        @media only screen and (max-width: $bp-lg) {
            height: 90px;
        }

        @media only screen and (max-width: $bp-md) {
            height: 78px;
            margin-bottom: 20px;
        }

        &:last-of-type{
            margin-bottom: 0;
        }
    }
}
