@import '@styles/vars.scss';

h1, h2, h3, h4, h5, h6, p {
    margin: 0;
    padding: 0;
}


.h1{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 60px;
    line-height: 120%;
    letter-spacing: 0.15px;
}

.h2{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 50px;
    line-height: 120%;
    letter-spacing: 0.15px;
    
    @media only screen and (max-width: $bp-xl){
        
    }
    
    @media only screen and (max-width: $bp-lg){
        font-size: 38px;
    }
    
    @media only screen and (max-width: $bp-md){
        font-size: 24px;
    }
}

.h3{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 30px;
    line-height: 120%;
    letter-spacing: 0.15px;

    @media only screen and (max-width: $bp-lg) {
        font-size: 24px;
    }

    @media only screen and (max-width: $bp-md) {
        font-size: 20px;
    }

    &.text-light{
        line-height: 150%;
    }
}

.h4{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 25px;
    line-height: 120%;
    letter-spacing: 0.15px;
}

.h5{
    font-family: 'Raleway';
    font-weight: 700;
    font-size: 20px;
    line-height: 120%;
    letter-spacing: 0.15px;
}

.text{
    font-size: 16px;
    line-height: 120%;
    letter-spacing: 0.15px;

    &-xs{
        font-size: 12px;
        line-height: 120%;
    }

    &-md{
        font-size: 14px;
        line-height: 120%;
    }

    &-lg{
        font-size: 18px;
        line-height: 120%;
        letter-spacing: 0.15px;
    }

    &-xl{
        font-size: 20px;
        line-height: 150%;
        letter-spacing: 0.15px;
    }

    &-light{
        font-weight: 300;
    }
    
    &-medium{
        font-weight: 500;
    }

    &-semibold{
        font-weight: 600;
    }

    &-bold{
        font-weight: 700;
    }

    &-raleway{
        font-family: 'Raleway';
    }

    &-inter{
        font-family: 'Inter';
    }
}