@import '@styles/vars.scss';
@import '@styles-mixins/media.scss';
@import '@styles-mixins/hoverEffects.scss';

.slider {

    .swiper{
        &-wrapper{
        height: auto;
        }
    }

    &__nav {
        display: none;


        @include media(from_m) {
        display: flex;
        margin-left: -8px;
        margin-top: 16px;
        }

        @include media(from_l) {
        margin-left: -16px;
        margin-top: 24px;
        }
    }

    &__nav-button {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        box-sizing: border-box;
        border: 1px solid $blue;
        margin-left: 8px;

        background-repeat: no-repeat;
        background-size: 24px auto;

        @include defaultHover();

        @include media(from_l) {
        width: 48px;
        height: 48px;

        border-width: 2px;
        margin-left: 16px;

        background-size: 32px;
        }
    }

    &__nav-button_type_prev {
        background-image: url("data:image/svg+xml,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_430_4575)'%3E%3Cpath d='M11.3605 12.4704L16.3838 17.4938L14.9488 18.9287L8.49056 12.4704L14.9488 6.01215L16.3838 7.4471L11.3605 12.4704Z' fill='%233AB3FE'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_430_4575'%3E%3Crect width='24.3556' height='24.3556' fill='white' transform='translate(24.7275 24.6475) rotate(-180)'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E%0A");

        background-position: 2px center;

        @include media(from_l) {
        background-position: 4px center;
        }
    }

    &__nav-button_type_next {
        background-image: url("data:image/svg+xml,%3Csvg width='25' height='25' viewBox='0 0 25 25' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg clip-path='url(%23clip0_430_4572)'%3E%3Cpath d='M13.6444 12.47L8.62109 7.44667L10.056 6.01172L16.5143 12.47L10.056 18.9283L8.62109 17.4933L13.6444 12.47Z' fill='%233AB3FE'/%3E%3C/g%3E%3Cdefs%3E%3CclipPath id='clip0_430_4572'%3E%3Crect width='24.3556' height='24.3556' fill='white' transform='translate(0.277344 0.291992)'/%3E%3C/clipPath%3E%3C/defs%3E%3C/svg%3E%0A");

        background-position: 4px center;

        @include media(from_l) {
        background-position: 8px center;
        }
    }

    .swiper-button-disabled {
        opacity: 0.24;
    }

    &__dot-pagination {
        position: static;
        display: flex;
        justify-content: center;
        margin-top: 32px;
        margin-left: -4px;

        transform: none!important;
        width: 100%!important;

        @include media(from_sm) {
        margin-top: 48px;
        margin-left: -8px;
        }

        @include media(from_m) {
        display: none;
        }
    }

    .swiper-pagination-bullet {
        width: 8px;
        height: 8px;
        margin-left: 4px;

        border-radius: 50%;

        background-color: #B9B9B9;
        transform: scale(0);

        @include media(from_sm) {
        width: 12px;
        height: 12px;

        margin-left: 8px;
        }
    }

    .swiper-pagination-bullet-active-main {
        transform: scale(1);
    }
    
    .swiper-pagination-bullet-active {
        background-color: #3AB3FE;
    }

    &__navigation {
        position: static;
        display: flex;
        display: inline-flex;
        align-items: center;
        justify-content: flex-start;
        margin-top: 34px;

        @media only screen and (max-width: $bp-xl){
            margin-top: 22px;
        }
        
        @media only screen and (max-width: $bp-lg){
            margin-top: 40px;
        }
        
        @media only screen and (max-width: $bp-md){
            margin-top: 24px;
        }
    } 

    &__button {
        margin: 0;
        box-sizing: border-box;
        position: static;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 45px;
        height: 45px;
        border-radius: 50%;
        flex-shrink: 0;
        border: 2px solid $blue;
        fill: $blue;
        background-color: transparent;
        transition: background-color 0.35s ease, fill 0.35s ease, color 0.35s ease, opacity 0.35s ease;

        &:hover{
            background-color: $blue;
            color: #fff;
            fill: #fff;
        }

        &::after, 
        &::before{
            content: none;
            
        }
    }

    &__button-icon{
        flex-shrink: 0;
        width: 32px;
        height: 32px;
    }

      
    &__pagination{
        position: static;
        width: auto;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 30px;

    }

    &__pagination-item{
        cursor: pointer;
        display: block;
        width: 12px;
        height: 12px;
        margin: 0 4px;
        border-radius: 50%;
        background-color: $gray;
        transition: background-color 0.35s ease;

        @media only screen and (max-width: $bp-lg){
            width: 7px;
            height: 7px;
            margin: 0 2px;
        }

        &.swiper-pagination-bullet-active{
            background-color: $blue;
        }
    }

    &__fraction-item{
        display: inline-flex;
        align-items: center;
        font-family: 'Inter';
        color: #B9B9B9;
        font-size: 14px;
        line-height: 24px;
        margin-left: 12px;
        transition: font-size 0.15s ease, color 0.35s ease;

        &::after{
            content:"";
            display: inline-flex;
            margin-left: 0px;
            height: 1px;
            width: 0px;
            background-color: #000;
            opacity: 0.2;
            transition: margin 0.45s ease, width 0.45s ease;
        }

        &--active{
            font-size: 20px;
            color: $blue;

            &::after{
                width: 50px;
                margin-left: 20px;
            }
        }

        &:first-of-type{
            margin-left: 0;
        }

        &:last-of-type::after{
            order: -1;
        }

        &--active:last-of-type{
            &::after{
                margin-left: 0;
                margin-right: 20px;
            }
        }

    }
}
