@import "@styles/vars.scss";
@import "@styles-mixins/media.scss";
@import "@styles-mixins/hoverEffects.scss";

.socials {
  &__partner {
    display: flex;
    flex-direction: column;

    text-decoration: none;

    @include defaultHover();
  }

  &__partner-label {
    margin: 0 0 16px;

    font-family: "Inter", sans-serif;;
    font-weight: 400;
    font-size: 16px;
    line-height: 150%;
    color: $color_text_secondary;
  }

  &__partner-logo {
    width: 180px;
    height: auto;

    @include media(from_sm) {
      width: 272px;
    }
  }

  &__partner-logo img {
    width: 100%;
  }

  &__media {
    display: flex;
  }

  &__media-item {
    display: inline-block;
    width: 32px;
    height: 32px;

    background-repeat: no-repeat;
    background-size: 24px auto;
    background-position: center;

    @include defaultHover();

    @include media(from_sm) {
      width: 48px;
      height: 48px;

      background-size: 32px auto;
    }

    @include media(from_m) {
      width: 24px;
      height: 24px;

      background-size: 16px auto;
    }

    @include media(from_l) {
      background-size: 24px auto;
    }
  }

