@import '@styles/vars.scss';

.about-banner {
    margin-top: 105px;
    
    @media only screen and (max-width: $bp-xl) {
        margin-top: 90px;
    }
    
    @media only screen and (max-width: $bp-lg) {
        margin-top: 75px;
    }
    
    @media only screen and (max-width: $bp-md) {
        margin-top: 64px;
    }
    
    &__main {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        text-decoration: none;
        color: #fff;
        background-color: $blue;
        border-radius: 30px;
        min-height: 315px;

        @media only screen and (max-width: $bp-lg) {
            flex-direction: column;
            align-items: flex-start;
        }

        &:hover{
            background-color: $blue-light;
        }
    }


    &__logo {
        object-fit: contain;
        width: 324px;
        max-height: 242px;
        height: auto;

        @media only screen and (max-width: $bp-xl) {
            width: 280px;
        }

        @media only screen and (max-width: $bp-lg) {
            width: 220px;
            margin: 22px 15px 0 auto;
        }

        @media only screen and (max-width: $bp-md) {
            width: 188px;
        }
    }

    &__title {
        margin: auto 30px;
        display: flex;
        flex-direction: column;
        font-size: 40px;
        line-height: 120%;
        letter-spacing: 0.15px;

        @media only screen and (max-width: $bp-xl) {
            font-size: 35px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin: -28px 15px 0 28px;
        }

        span{
            margin-left: 96px;

            @media only screen and (max-width: $bp-xl) {
                margin-left: 40px;
            }

            @media only screen and (max-width: $bp-lg) {
                margin-left: 0;
            }
        }
    }

    &__button {
        width: 68px;
        height: 68px;
        margin: auto 16px 16px 20px;

        .button__icon{
            width: 48px;
            height: 48px;

            @media only screen and (max-width: $bp-xl) {
                width: 42px;
                height: 42px;
            }
        }

        @media only screen and (max-width: $bp-xl) {
            width: 58px;
            height: 58px;
            margin: auto 14px 10px 15px;
        }

        @media only screen and (max-width: $bp-lg) {
            margin-left: auto;
        }
    }
}
