@import '@styles/vars.scss';

.checkbox {
    cursor: pointer;
    display: flex;
    width: 100%;
    color: $gray-dark;
    
    &:hover &__icon-wrapper{
        border-color: $blue;
    }

    &__input {
        display: none;
    }

    &__input:checked + &__wrapper &__icon-wrapper{
        background-color: $blue;
        border-color: $blue;
    }
    
    &__input:checked + &__wrapper &__icon{
        transform: scale(1);
    }

    &__wrapper {
        display: flex;
    }

    &__icon-wrapper {
        box-sizing: border-box;
        width: 18px;
        height: 18px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-shrink: 0;
        background-color: transparent;
        border-radius: 3px;
        border: 2px solid #C2CFD3;
        margin: 4px 0;
        transition: border-color 0.25s ease, background-color 0.25s ease;
    }

    &__icon {
        fill: #fff;
        width: 18px;
        height: 18px;
        flex-shrink: 0;
        transform: scale(0);
        transition: transform 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.15s;
    }

    &__content {
        margin-left: 16px;
    }

    &__text {
        display: inline-flex;
        line-height: 26px;
    }
}