.tech {
  min-height: 100vh;
  box-sizing: border-box;
  padding: 72px 0 96px;

  &__container {
    width: 100%;
    max-width: 1024px;
    box-sizing: border-box;
    padding: 56px 64px 72px;
    margin: 0 auto;
    background-color: #fff;
  }

  &__title {
    margin: 0 0 24px;
    font-size: 48px;
    line-height: 56px;
  }

  &__sitemap {
    width: 100%;
    max-width: 320px;
  }

  &__component-box {
    margin: 40px auto;
  }
}
